import React, { useEffect, useRef, useState } from "react";
import { Form, Modal } from "react-bootstrap";
import { useHistory } from "react-router-dom";
import {
  ApiDelete,
  ApiGet,
  ApiPost,
  Bucket,
} from "../../../helpers/API/ApiData";
import { MdCancel } from "react-icons/md";
import { ErrorToast, SuccessToast } from "../../../helpers/Toast";
import Swal from "sweetalert2";

const Banners = () => {
  const fileInputRef = useRef(null);
  const history = useHistory();
  const [modal, setModal] = useState(false);
  const [Data, setData] = useState([]);
  const [newData, setNewData] = useState({
    image: "",
    isEng: "",
  });
  const [images, setImages] = useState([
    "https://images-eu.ssl-images-amazon.com/images/G/31/sthaneka/Alphonsa/GW/NOV/BAU/GW_HERO_PC_1_2x._CB542130280_.jpg",
    "https://images-eu.ssl-images-amazon.com/images/G/31/sthaneka/Alphonsa/GW/NOV/BAU/GW_HERO_PC_1_2x._CB542130280_.jpg",
  ]);

  const getBanners = async () => {
    await ApiGet(`/banner`)
      .then((res) => {
        console.log("res", res?.data?.data);
        setData(res?.data?.data);
        //   setShortingData(res?.data?.data);
      })
      .catch((err) => {
        console.log("err", err);
      });
  };

  useEffect(() => {
    getBanners();
  }, []);

  const imageChange = (e) => {
    const file = e.target.files[0];
    console.log("file = ", file);

    if (file) {
      const fileURL = URL.createObjectURL(file);
      console.log("file = ", fileURL);

      setNewData({ ...newData, image: fileURL });
    }
  };

  const onDelete = async (id) => {
    console.log("id = ", id);

    await ApiDelete(`/banner/${id}`)
      .then(() => {
        Swal.fire({
          text: "You have Deleted Successfully!!!",
          icon: "success",
          confirmButtonText: "Ok, Got it!",
          confirmButtonColor: "#338DE6",
        });
        getBanners();
      })
      .catch((err) => ErrorToast(err?.message));
  };

  const handleSaveImage = async () => {
    const file = fileInputRef.current.files[0];
    console.log("newData", newData);

    if (file) {
      let formData = new FormData();

      formData.append("image", file);

      const isEngValue = newData?.isEng === "true" || newData?.isEng === true;
      formData.append("isEng", isEngValue);

      console.log("formData=", formData);

      try {
        const res = await ApiPost(`/banner/${isEngValue}`, formData)
          .then((res) => {
            setData(res?.data?.data?.response);
            getBanners();

            SuccessToast(res?.data?.message);
          })
          .catch((err) => {
            ErrorToast(err?.message);
          });

        setImages([...images, res?.data?.data]);
        setModal(false);
        setNewData({ image: "", isEng: "" });
        fileInputRef.current.value = "";
      } catch (err) {
        console.error("Error uploading image:", err);
      }
    }
  };

  return (
    <>
      <div className="d-flex justify-content-between mb-14">
        <div className="title">
          <div className="fs-20px fw-bolder">Banner List</div>
          <div>
            <span
              role="button"
              onClick={() => history.push("/dashboard")}
              className="text-hover-info text-muted"
            >
              Home
            </span>{" "}
            -{" "}
            <span className="text-muted" role="button">
              {" "}
              Banner
            </span>
          </div>
        </div>
      </div>

      <div className="container-fluid">
        <div className="row">
          <div className="col-md-6">
            <div className="card mb-4">
              <div className="card-body d-flex flex-column">
                <h2 className="card-title">English Images</h2>
                <div
                  className="task-list mt-5 pt-5"
                  style={{
                    maxHeight: "610px",
                    overflowY: "auto",
                    overflowX: "hidden",
                  }}
                >
                  <div className="row">
                    {Data?.english?.map((item, index) => (
                      <div key={index} className="mb-5 col-md-6">
                        <div className="d-flex align-items-start mb-5">
                          <img
                            src={
                              item?.image
                                ? `${Bucket}${item?.image}`
                                : "https://static.vecteezy.com/system/resources/thumbnails/014/066/812/small/discount-ribbon-banner-icon-for-graphic-design-logo-website-social-media-mobile-app-ui-illustration-vector.jpg"
                            }
                            alt="defaultImage"
                            className="w-100"
                            style={{ maxWidth: "70%", height: "5rem" }}
                          />
                          <button
                            className="btn btn-danger btn-sm ms-2 d-flex align-items-center justify-content-center"
                            onClick={() => {
                              Swal.fire({
                                text: `Are you sure you want to Delete ?`,
                                icon: "warning",
                                showCancelButton: true,
                                showConfirmButton: true,
                                confirmButtonText: `Yes, Delete`,
                                confirmButtonColor: "#D72852",
                                cancelButtonColor: "black",
                                cancelButtonText: "No, Cancel",
                              }).then((res) => {
                                if (res.isConfirmed) {
                                  onDelete(item?._id);
                                }
                              });
                            }}
                            style={{
                              width: "30px",
                              height: "30px",
                              fontSize: "20px",
                            }}
                          >
                            &times;
                          </button>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
                <div className="card-toolbar">
                  <a
                    className="btn btn1 btn-color font-weight-bolder btn-sm ms-4"
                    onClick={() => {
                      setModal(true);
                      setNewData({
                        image: "",
                        isEng: true,
                      });
                    }}
                  >
                    Add +
                  </a>
                </div>
              </div>
            </div>
          </div>

          <div className="col-md-6">
            <div className="card mb-4">
              <div className="card-body d-flex flex-column">
                <h2 className="card-title">Arabic Images</h2>
                <div
                  className="task-list mt-5 pt-5"
                  style={{
                    maxHeight: "610px",
                    overflowY: "auto",
                    overflowX: "hidden",
                  }}
                >
                  <div className="row">
                    {Data?.arabic?.map((item, index) => (
                      <div key={index} className="mb-5 col-md-6">
                        <div className="d-flex align-items-start mb-5">
                          <img
                            src={
                              item?.image
                                ? `${Bucket}${item?.image}`
                                : "https://static.vecteezy.com/system/resources/thumbnails/014/066/812/small/discount-ribbon-banner-icon-for-graphic-design-logo-website-social-media-mobile-app-ui-illustration-vector.jpg"
                            }
                            alt="defaultImage"
                            className="w-100"
                            style={{ maxWidth: "70%", height: "5rem" }}
                          />
                          <button
                            className="btn btn-danger btn-sm ms-2 d-flex align-items-center justify-content-center"
                            onClick={() => {
                              Swal.fire({
                                text: `Are you sure you want to Delete ?`,
                                icon: "warning",
                                showCancelButton: true,
                                showConfirmButton: true,
                                confirmButtonText: `Yes, Delete`,
                                confirmButtonColor: "#D72852",
                                cancelButtonColor: "black",
                                cancelButtonText: "No, Cancel",
                              }).then((res) => {
                                if (res.isConfirmed) {
                                  onDelete(item?._id);
                                }
                              });
                            }}
                            style={{
                              width: "30px",
                              height: "30px",
                              fontSize: "20px",
                            }}
                          >
                            &times;
                          </button>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
                <div className="card-toolbar">
                  <a
                    className="btn btn1 btn-color font-weight-bolder btn-sm ms-4"
                    onClick={() => {
                      setModal(true);
                      setNewData({
                        image: "",
                        isEng: false,
                      });
                    }}
                  >
                    Add +
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Modal show={modal} onHide={() => setModal(false)} centered>
        <Modal.Header closeButton>
          <Modal.Title>Upload Image</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row">
            <Form.Group className="col-md-12">
              <Form.Label>Image</Form.Label>
              <input
                ref={fileInputRef}
                type="file"
                className="form-control"
                name="image"
                id="image"
                onChange={imageChange}
              />
              <span className="errorInput"></span>
            </Form.Group>
            {newData?.image && (
              <div className="position-relative w-auto">
                <img
                  src={newData?.image}
                  alt="Preview"
                  style={{ width: "100px", height: "100px" }}
                  className="rounded-3"
                />
                <span
                  className="imageRemovoIconSpan"
                  onClick={() => {
                    setNewData({ ...newData, image: "" });
                    fileInputRef.current.value = null; // Clear the file input
                  }}
                >
                  <MdCancel color="red" fontSize={20} />
                </span>
              </div>
            )}
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button
            className="btn btn1 btn-color"
            onClick={handleSaveImage} // Call the save function on button click
          >
            Save
          </button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default Banners;
