import React, { useEffect, useMemo, useState } from "react";
import objectPath from "object-path";
import ApexCharts from "apexcharts";
import "react-datepicker/dist/react-datepicker.css";
import { useHistory } from "react-router-dom";
import { ApiPost } from "../../../helpers/API/ApiData";
import { ErrorToast } from "../../../helpers/Toast";
import ProgressBar from "react-bootstrap/ProgressBar";
import { useHtmlClassService } from "../../../_metronic/layout";
import { Form} from "react-bootstrap";
import CanvasJSReact from "@canvasjs/react-charts";

export default function ShareAnalysis({ className }) {
  const history = useHistory();
  const [data, setData] = useState([]);
  const [categoryData, setCategoryData] = useState([]);
  const [selectTasks, setSelectedTasks] = useState("");
  const [filteredTasks, setFilteredTasks] = useState([]);
  const [popularTaskData, setPopularTaskData] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState("");
  const [categories, setCategories] = useState([]);
  // const [viewModal, setViewModal] = useState(false);
  // const [platformData, setPlatformData] = useState([]);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");


  const fetchData = async (page, limit, search) => {
    let body = { page, limit, search };
    try {
      const res = await ApiPost("/share-track/analysis", body);
      setData(res?.data?.data?.response);
      setCategoryData(res?.data?.data?.response?.categoryData);
    } catch (err) {
      ErrorToast(err?.message || "Something went wrong");
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  // const viewBtnClick = (row) => {
  //   if (row.plateform) {
  //     const dataPoints = Object.keys(row.plateform).map((platform) => ({
  //       label: platform,
  //       y: row.plateform[platform]?.shareCount || 0,
  //     }));

  //     setPlatformData(dataPoints);
  //     setViewModal(true);
  //   }
  // };
    // const columns = [
  //   {
  //     dataField: "_id",
  //     text: "ID",
  //     formatter: (cell, row, i) => {
  //       return <div>{(currentpage - 1) * pagesize + i + 1 || "-"}</div>;
  //     },
  //   },
  //   {
  //     dataField: "categoryName",
  //     text: "Category Name",
  //     formatter: (cell) => <div>{cell || "-"}</div>,
  //   },
  //   {
  //     dataField: "shareCount",
  //     text: "Share Count",
  //     formatter: (cell) => <div>{cell || "-"}</div>,
  //   },
  //   {
  //     dataField: "percentage",
  //     text: "Percentage",
  //     formatter: (cell) => <div>{cell || "-"}</div>,
  //   },
  //   {
  //     dataField: "plateform",
  //     text: "Action",
  //     sort: true,
  //     formatter: (cell, row) => {
  //       return (
  //         <>
  //           <a onClick={() => viewBtnClick(row)}>
  //             <span className="text-primary">View Detail</span>
  //           </a>
  //         </>
  //       );
  //     },
  //   },
  // ];

  const filterAndNormalizePercentages = async (selectedCategory) => {
    try {
      const payload = { startDate, endDate };
      const res = await ApiPost(
        `/share-track/category/${selectedCategory}`,
        payload
      );
      console.log("payload", payload);
      const taskData = res?.data?.data?.response || [];
      setPopularTaskData(taskData);
      setFilteredTasks(taskData);
    } catch (err) {
      ErrorToast(err?.message);
    }
  };

  useEffect(() => {
    if (!selectedCategory) {
      setFilteredTasks(categories?.taskData || []);
    } else if (startDate || endDate) {
      filterAndNormalizePercentages(selectTasks, startDate, endDate);
    } else {
      const filtered = categories?.taskData?.filter(
        (task) => task?.category?._id === selectedCategory
      );
      setFilteredTasks(filtered || []);
    }
  }, [selectedCategory, categories]);

  const uiService = useHtmlClassService();

  const layoutProps = useMemo(() => {
    return {
      colorsGrayGray500: objectPath.get(
        uiService.config,
        "js.colors.gray.gray500"
      ),
      colorsGrayGray200: objectPath.get(
        uiService.config,
        "js.colors.gray.gray200"
      ),
      colorsGrayGray300: objectPath.get(
        uiService.config,
        "js.colors.gray.gray300"
      ),
      colorsThemeBaseDanger: objectPath.get(
        uiService.config,
        "js.colors.theme.base.danger"
      ),
      fontFamily: objectPath.get(uiService.config, "js.fontFamily"),
    };
  }, [uiService]);

  useEffect(() => {
    const element = document.getElementById("kt_mixed_widget_1_chart");
    const element2 = document.getElementById("kt_mixed_widget_2_chart");
    const element3 = document.getElementById("kt_mixed_widget_3_chart");

    if (!element || !element2 || !element3) return;

    const options = "";
    const options2 = "";
    const options3 = "";

    const chart = new ApexCharts(element, options);
    const chart2 = new ApexCharts(element2, options2);
    const chart3 = new ApexCharts(element3, options3);

    chart.render();
    chart2.render();
    chart3.render();

    return () => {
      chart.destroy();
      chart2.destroy();
      chart3.destroy();
    };
  }, [layoutProps]);

  useEffect(() => {
    if (data) {
      const options = getCategoryChart();
      const chart = new ApexCharts(
        document.getElementById("categoryChart"),
        options
      );
      chart.render();
      return () => {
        chart.destroy();
      };
    }
  }, [data]);

  function getCategoryChart() {
    console.log("data", data);

    let chartData = [];
    let categories = [];

    if (data?.categoryData?.length > 0) {
      chartData = data.categoryData.map((item) => item.shareCount);
      categories = data.categoryData.map((item) => item.categoryName);
    } else {
      chartData = [0, 0, 0, 0, 0, 0, 0];
      categories = ["No Data"];
    }

    const colorPalette = [
      "rgba(0, 123, 255, 0.7)",
      "rgba(40, 167, 69, 0.7)",
      "rgba(220, 53, 69, 0.7)",
      "rgba(255, 193, 7, 0.7)",
      "rgba(23, 162, 184, 0.7)",
      "rgba(108, 117, 125, 0.7)",
      "rgba(255, 255, 255, 0.7)",
      "rgba(255, 99, 132, 0.7)",
      "rgba(75, 192, 192, 0.7)",
      "rgba(153, 102, 255, 0.7)",
    ];

    const barColors = chartData.map((_, index) => {
      return {
        type: "linear",
        gradient: {
          shade: "light",
          type: "vertical",
          shadeIntensity: 0.3,
          gradientToColors: [colorPalette[index % colorPalette.length]],
          stops: [0, 100],
        },
      };
    });

    return {
      chart: {
        type: "bar",
        height: 350,
        toolbar: {
          show: false,
        },
      },
      series: [
        {
          name: "categoryData",
          data: chartData,
          fill: {
            colors: barColors,
          },
        },
      ],
      xaxis: {
        categories: categories,
      },
      plotOptions: {
        bar: {
          distributed: true,
        },
      },
    };
  }

  var CanvasJSChart = CanvasJSReact.CanvasJSChart;
  const darkColors = [
    "rgba(220, 53, 69, 0.7)",
    "rgba(40, 167, 69, 0.7)",
    "rgba(255, 193, 7, 0.7)",
    "rgba(0, 123, 255, 0.7)",
    "rgba(220, 53, 69, 0.7)",
    "rgba(23, 162, 184, 0.7)",
    "rgba(108, 117, 125, 0.7)",
    "rgba(255, 255, 255, 0.7)",
    "rgba(255, 99, 132, 0.7)",
    "rgba(75, 192, 192, 0.7)",
    "rgba(153, 102, 255, 0.7)",
  ];

  const options = {
    exportEnabled: true,
    animationEnabled: true,
    // subtitles: [
    //   {
    //     text: "Task Data",
    //   },
    // ],
    data: [
      {
        type: "doughnut",
        startAngle: 90,
        indexLabel: "",
        yValueFormatString: "#,##0",
        toolTipContent: "{name}: {y}",
        dataPoints:
          filteredTasks?.length > 0
            ? filteredTasks.map((item, index) => ({
                name: item?.name || "Unnamed Task",
                y: item?.shareCount || 0,
                color: darkColors[index % darkColors.length],
              }))
            : [{ name: "No Data", y: 100, color: "grey" }],
      },
    ],
  };

  return (
    <>
      <div className="d-flex justify-content-between mb-4">
        <div className="title">
          <div className="fs-20px fw-bolder">Share Analysis List</div>
          <div>
            <span
              role="button"
              onClick={() => history.push("/dashboard")}
              className="text-hover-info text-muted"
            >
              Home
            </span>{" "}
            -{" "}
            <span className="text-muted" role="button">
              {" "}
              Share Track - Share Analysis
            </span>
          </div>
        </div>
      </div>

      <div className="row" style={{ marginBottom: "30px" }}>
        <div className={`${className} col-lg-6 col-xxl-5 `}>
          <div
            className="backgroundcard rounded shadow overflow-scroll"
            style={{ height: "530px" }}
          >
            <h2 className="justify-content-center my-9 pl-5">Categorie Data</h2>
            {data?.categoryData?.map((item, index) => {
              return (
                <>
                  <div
                    className="d-flex gap-6 mt-5  align-items-center pb-2"
                    key={index}
                  >
                    <div className="col-2">
                      <h5 className="">{item?.categoryName}:</h5>
                    </div>
                    <div className="col-9">
                      <ProgressBar
                        variant="success"
                        animated={true}
                        now={item?.percentage ? item?.percentage : 0}
                      />
                    </div>
                    <div className="col">
                      <h6 className="d-flex justify-content-end">
                        {item?.percentage ? item?.percentage : 0}%
                      </h6>
                    </div>
                  </div>
                </>
              );
            })}
          </div>
        </div>

        <div className={`${className} col-lg-6 col-xxl-5`}>
          <div
            className="backgroundcard rounded shadow overflow-scroll"
            style={{ height: "530px" }}
          >
            <h2 className="justify-content-center my-9 pl-5">Task Data</h2>
            {data?.taskData?.map((item, index) => {
              return (
                <>
                  <div
                    className="d-flex gap-6 mt-5  align-items-center pb-2"
                    key={index}
                  >
                    <div className="col-3">
                      <h5 className="">{item?.name}:</h5>
                    </div>
                    <div className="col-8">
                      <ProgressBar
                        variant="success"
                        animated={true}
                        now={item?.percentage ? item?.percentage : 0}
                      />
                    </div>
                    <div className="col">
                      <h6 className="d-flex justify-content-end">
                        {item?.percentage ? item?.percentage : 0}%
                      </h6>
                    </div>
                  </div>
                </>
              );
            })}
          </div>
        </div>
      </div>

      <div className="row" style={{ marginBottom: "30px" }}>
        <div className={`${className} col-xxl-5 col-lg-6 col-12`}>
          <div
            className={`${className} w-full backgroundcard rounded shadow overflow-scroll`}
            style={{ height: "560px" }}
          >
            <div className="d-flex flex-column flex-lg-row justify-content-between my-3 my-lg-9 pl-0 pl-lg-5">
              <h2 className="mb-3 mb-lg-0">Category Data</h2>
            </div>
            <div id="categoryChart" style={{ height: "400px" }}></div>
          </div>
        </div>

        <div className={`${className} col-xxl-5 col-lg-6 col-12`}>
          <div
            className={`${className} w-full backgroundcard rounded shadow overflow-scroll`}
            style={{ height: "560px" }}
          >
            <div className="d-flex justify-content-between pl-5">
              <h2 className="pt-5">Tasks Data</h2>
              <Form.Group className="col-sm-6">
                <Form.Control
                  as="select"
                  custom
                  id="country"
                  required
                  className="form-control"
                  name="getcategories"
                  onChange={(e) => {
                    const selectedCategory = e.target.value;
                    setSelectedTasks(selectedCategory);
                    filterAndNormalizePercentages(selectedCategory);
                  }}
                  value={selectTasks}
                >
                  <option value="">All Categories</option>
                  {categoryData?.map((v) => (
                    <option key={v?._id} value={v?._id}>
                      {v?.categoryName}
                    </option>
                  ))}
                </Form.Control>
              </Form.Group>
            </div>

            <div className="d-flex justify-content-between pl-5">
              <Form.Group className="col-sm-6">
                <div className="col-sm-6">
                  <label>Start Date</label>
                  <input
                    type="date"
                    value={startDate}
                    onChange={(e) => {
                      console.log("startDate", e.target.value);
                      setStartDate(e.target.value);
                    }}
                  />
                </div>
              </Form.Group>
              <Form.Group className="col-sm-6">
                <div className="col-sm-6">
                  <label>End Date</label>
                  <input
                    type="date"
                    value={endDate}
                    onChange={(e) => {
                      console.log("endDate", e.target.value);
                      setEndDate(e.target.value);
                    }}
                  />
                </div>
              </Form.Group>
            </div>

            {filteredTasks?.length > 0 &&
            filteredTasks.some((task) => task.shareCount > 0) ? (
              <CanvasJSChart options={options} />
            ) : (
              <CanvasJSChart
                options={{
                  animationEnabled: true,
                  theme: "#666666",
                  data: [
                    {
                      type: "doughnut",
                      startAngle: 90,
                      indexLabel: "",
                      yValueFormatString: "#,##0",
                      toolTipContent: "{name}: {y}",
                      dataPoints: [
                        { y: 100, name: "No Data", color: "#666666" },
                        { y: 0, name: "Placeholder", color: "transparent" },
                      ],
                    },
                  ],
                }}
              />
            )}
          </div>
        </div>
      </div>

      {/* <div
        className="d-flex flex-column"
        id="kt_content"
        style={{ marginBottom: "30px" }}
      >
        <div className="card card-custom">
          <div className="card-header flex-wrap border-0 pt-6 pb-0">
            <div className="card-title">
              <div className="input-icon">
                <input
                  type="text"
                  className="form-control bg-light"
                  placeholder="Search by Email"
                  value={searching}
                  onChange={handlesearch}
                />
                <span>
                  <i className="flaticon2-search-1 text-muted"></i>
                </span>
              </div>
            </div>
          </div>

          <div className="card-body">
            <BootstrapTable
              wrapperClasses="table-responsive"
              bordered={false}
              classes="table table-head-custom table-vertical-center"
              bootstrap4
              keyField="_id"
              data={categoryData}
              columns={columns}
              noDataIndication="No data available"
            />
            <div className="d-flex justify-content-between align-items-center pt-4">
              <Pagination
                count={totalpage}
                page={currentpage}
                onChange={onPaginationChange}
                variant="outlined"
                shape="rounded"
              />
              <select
                className="form-control w-auto"
                value={pagesize}
                onChange={handleonchnagespagination}
              >
                <option value={10}>10</option>
                <option value={20}>20</option>
                <option value={30}>30</option>
                <option value={50}>50</option>
              </select>
            </div>
          </div>
        </div>
      </div>

      <Modal
        show={viewModal}
        onHide={() => setViewModal(false)}
        centered
        className="modal-width"
      >
        <Modal.Header closeButton>
          <Modal.Title>Share Detail</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div style={{ width: "100%", height: "auto", overflowX: "auto" }}>
            <CanvasJSChart
              options={{
                exportEnabled: true,
                animationEnabled: true,
                subtitles: [{ text: "Platform Data" }],
                data: [
                  {
                    type: "column",
                    indexLabel: "{y}",
                    yValueFormatString: "#,##0",
                    toolTipContent: "{label}: {y}",
                    dataPoints: platformData,
                  },
                ],
              }}
            />
          </div>
          <div className="mt-7">
            <ul className="list-group">
              {platformData.map((item, index) => (
                <li
                  key={index}
                  className="list-group-item d-flex justify-content-between"
                >
                  <span>{item.label}</span>
                  <span>{item.y}</span>
                </li>
              ))}
            </ul>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button className="btn btn-color" onClick={() => setViewModal(false)}>
            Cancel
          </button>
        </Modal.Footer>
      </Modal> */}
    </>
  );
}
