import React, { useEffect, useState } from "react";
import { ErrorToast } from "../../../helpers/Toast";
import { ApiGet } from "../../../helpers/API/ApiData";
import Rating from "react-rating";
import { FaStar, FaRegStar } from "react-icons/fa";
import { useHistory } from "react-router-dom";

function Feedback({ className }) {
  const [category, setcategory] = useState([]);
  const history = useHistory();

  const fetchDatafeedbacks = async () => {
    await ApiGet("/categoryRating")
      .then((res) => {
        setcategory(res?.data?.data || []);
      })
      .catch((err) => {
        ErrorToast(err?.message);
      });
  };

  useEffect(() => {
    fetchDatafeedbacks();
  }, []);

  return (
    <>
      <div className="d-flex justify-content-between mb-14">
        <div className="title">
          <div className="fs-20px fw-bolder">Feedback List</div>
          <div>
            <span
              role="button"
              onClick={() => history.push("/dashboard")}
              className="text-hover-info text-muted"
            >
              Home
            </span>{" "}
            -{" "}
            <span className="text-muted" role="button">
              {" "}
              Feedback
            </span>
          </div>
        </div>
      </div>
      <div className="row gap-5 justify-content-center">
        {category.length > 0 ? (
          category.map((feedback) => (
            <>
              <a
                href={`/feedback-task?id=${feedback._id}`}
                // className="col-lg-3 col-xxl-2 px-7 backgroundcard rounded shadow"
                className="col-lg-3 col-xxl-2 d-flex flex-column align-items-center justify-content-center backgroundcard rounded shadow"
                style={{ cursor: "pointer" }}
              >
                <h5 className="justify-content-center my-9 pl-5">
                  {feedback.name}
                </h5>
                <div className="d-flex align-items-center my-9">
                  <Rating
                    initialRating={feedback.avgRating}
                    readonly
                    emptySymbol={<FaRegStar className="text-secondary" />}
                    fullSymbol={<FaStar className="text-warning" />}
                    fractions={2}
                  />
                </div>
              </a>
            </>
          ))
        ) : (
          <p className="text-center mt-5">No feedback available.</p>
        )}
      </div>
    </>
  );
}

export default Feedback;
