import React, { useEffect, useState } from "react";
import { ErrorToast } from "../../../helpers/Toast";
import { ApiGet } from "../../../helpers/API/ApiData";
import Rating from "react-rating";
import { FaStar, FaRegStar } from "react-icons/fa";
import BootstrapTable from "react-bootstrap-table-next";
import NoDataTable from "../../../common/noDataTable";
import { Modal, Button } from "react-bootstrap"; // Modal component from react-bootstrap

const TaskHistory = () => {
  const search = window.location.search;
  const params = new URLSearchParams(search);
  const FeedbackIdQuery = params.get("id");

  const [data, setData] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [selectedTask, setSelectedTask] = useState(null);
  const [loading, setLoading] = useState(false);

  const handleCardClick = async () => {
    await ApiGet(`/taskRating/${FeedbackIdQuery}`)
      .then((res) => {
        console.log("feedbacks", res);
        setData(res?.data?.data || []);
      })
      .catch((err) => {
        ErrorToast(err?.message);
      });
  };
  console.log("data", data);

  useEffect(() => {
    handleCardClick();
  }, []);

  const columns = [
    {
      dataField: "taskDetails.name",
      text: "Task Name",
    },
    {
      dataField: "avgRating",
      text: "Average Rating",
      formatter: (cell, row) => (
        <div className="d-flex align-items-center">
          <Rating
            initialRating={cell}
            readonly
            emptySymbol={<FaRegStar className="text-secondary" />}
            fullSymbol={<FaStar className="text-warning" />}
            fractions={2}
          />
        </div>
      ),
    },
    {
      dataField: "count",
      text: "Number of Reviews",
    },
    {
      dataField: "viewTask",
      text: "View Task",
      formatter: (cell, row) => (
        <Button variant="info" onClick={() => handleViewTaskClick(row?._id)}>
          View Task
        </Button>
      ),
    },
  ];
  const taskColumns = [
    {
      dataField: "completion.model_responses.userName",
      text: "User Name",
    },
    {
      dataField: "completion.model_responses.review",
      text: "review",
      formatter: (cell) => <span>{cell || "No feedback provided"}</span>,
    },

    {
      dataField: "completion.model_responses.rating",
      text: "Rating",
      formatter: (cell, row) => (
        <div className="d-flex align-items-center">
          <Rating
            initialRating={cell}
            readonly
            emptySymbol={<FaRegStar className="text-secondary" />}
            fullSymbol={<FaStar className="text-warning" />}
            fractions={2}
          />
        </div>
      ),
    },
  ];

  const handleViewTaskClick = (task) => {
    const ViewData = data?.filter((item) => {
      return item?._id === task;
    })?.[0];
    console.log("ViewData", ViewData);

    console.log("task", task);
    setSelectedTask(ViewData?.documents);
    setShowModal(true);
  };

  const handleCloseModal = () => setShowModal(false);
  console.log("selectedTask = ", selectedTask);

  return (
    <>
      {loading ? (
        <p>Loading...</p>
      ) : (
        <div
          className="d-flex flex-column flex-column-fluid h-100"
          id="kt_content"
        >
          <div className="card card-custom">
            <div className="card-header flex-wrap border-0 pt-6 pb-0 w-100">
              Task Feedback
            </div>
            <div className="card-body mb-5">
              <BootstrapTable
                wrapperClasses="table-responsive"
                headerWrapperClasses="border-0"
                bordered={false}
                classes="table table-head-custom table-vertical-center overflow-hidden"
                bootstrap4
                keyField="_id"
                data={data || []}
                columns={columns}
                noDataIndication={() => <NoDataTable />}
              />
            </div>
          </div>
        </div>
      )}
      {/* console.log(); */}

      {/* Modal for Viewing Task Details */}
      <Modal show={showModal} onHide={handleCloseModal} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>Task Details</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {console.log("selectedTask", selectedTask)}
          {selectedTask?.length > 0 ? (
            <>
              <BootstrapTable
                wrapperClasses="table-responsive"
                headerWrapperClasses="border-0"
                bordered={false}
                classes="table table-head-custom table-vertical-center overflow-hidden"
                bootstrap4
                keyField="_id"
                data={selectedTask || []}
                columns={taskColumns}
                noDataIndication={() => <NoDataTable />}
              />
            </>
          ) : (
            <p>Loading...</p>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseModal}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default TaskHistory;
