import React, { useEffect, useState } from "react";
import "react-datepicker/dist/react-datepicker.css";
import BootstrapTable from "react-bootstrap-table-next";
import { useHistory } from "react-router-dom";
import Pagination from "@material-ui/lab/Pagination";
import { ApiPost } from "../../../helpers/API/ApiData";
import { ErrorToast } from "../../../helpers/Toast";
import { Modal } from "react-bootstrap";

export default function ShareReport() {
  const history = useHistory();
  const [data, setData] = useState([]);
  const [totalpage, setTotalPage] = useState(0);
  const [currentpage, setCurrentPage] = useState(1);
  const [pagesize, setPageSize] = useState(10);
  const [searching, setSearching] = useState("");
  const [viewModal, setViewModal] = useState(false);
  const [viewData, setViewData] = useState({});

  const columns = [
    {
      dataField: "_id",
      text: "ID",
      formatter: (cell, row, i) => (
        <div>{(currentpage - 1) * pagesize + i + 1 || "-"}</div>
      ),
    },
    {
      dataField: "userName",
      text: "Name",
      formatter: (cell) => <div>{cell || "-"}</div>,
    },
    {
      dataField: "userData",
      text: "Email",
      formatter: (cell) => {
        if (!cell || typeof cell !== "object") return "-";
        return cell.email ? cell.email : "-";
      }
    },
    {
      dataField: "time",
      text: "Count",
      formatter: (cell) => <div>{Array.isArray(cell) ? cell.length : 0}</div>,
    },
    {
      dataField: "action",
      text: "Content",
      sort: true,
      formatter: (cell, row) => {
        return (
          <>
            <a onClick={() => viewBtnClick(row)}>
              <span className="text-primary">
                View Content
              </span>
            </a>
          </>
        );
      },
    },
  ];

  const fetchData = async (page, limit, search) => {
    let body = { page, limit, search };
    await ApiPost("/share-track/report", body)
      .then((res) => {
        console.log("res", res?.data?.data);
        setData(res?.data?.data?.response);
        setTotalPage(res?.data?.data?.state?.page_limit);
      })
      .catch((err) => ErrorToast(err?.message));
  };

  const handlePaginationChange = (e, i) => {
    setCurrentPage(i);
    fetchData(i, pagesize, searching);
  };

  const handleSearch = (e) => {
    const value = e.target.value;
    setSearching(value);
    setCurrentPage(1);
    fetchData(1, pagesize, value);
  };

  const handlePageSizeChange = (e) => {
    const value = parseInt(e.target.value);
    setPageSize(value);
    setCurrentPage(1);
    fetchData(1, value, searching);
  };

  useEffect(() => {
    fetchData(currentpage, pagesize, searching);
  }, []);

  const viewBtnClick = (row) => {
    setViewModal(true);
    setViewData(row);
  };

  return (
    <>
      <div className="d-flex justify-content-between mb-4">
        <div className="title">
          <div className="fs-20px fw-bolder">Share Report List</div>
          <div>
            <span
              role="button"
              onClick={() => history.push("/dashboard")}
              className="text-hover-info text-muted"
            >
              Home
            </span>{" "}
            -{" "}
            <span className="text-muted" role="button">
              {" "}
              Share Track - Share Report      
            </span>
          </div>
        </div>
      </div>

      <div className="d-flex flex-column h-100" id="kt_content">
        <div className="card card-custom">
          <div className="card-header flex-wrap border-0 pt-6 pb-0">
            <div className="card-title">
              <div className="input-icon">
                <input
                  type="text"
                  className="form-control bg-light"
                  placeholder="Search by Email"
                  value={searching}
                  onChange={handleSearch}
                />
                <span>
                  <i className="flaticon2-search-1 text-muted"></i>
                </span>
              </div>
            </div>
          </div>

          <div className="card-body">
            <BootstrapTable
              wrapperClasses="table-responsive"
              bordered={false}
              classes="table table-head-custom table-vertical-center"
              bootstrap4
              keyField="_id"
              data={data}
              columns={columns}
              noDataIndication="No data available"
            />
            <div className="d-flex justify-content-between align-items-center pt-4">
              <Pagination
                count={totalpage}
                page={currentpage}
                onChange={handlePaginationChange}
                variant="outlined"
                shape="rounded"
              />
              <select
                className="form-control w-auto"
                value={pagesize}
                onChange={handlePageSizeChange}
              >
                <option value={10}>10</option>
                <option value={20}>20</option>
                <option value={30}>30</option>
                <option value={50}>50</option>
              </select>
            </div>
          </div>
        </div>
      </div>

      <Modal
        show={viewModal}
        onHide={() => setViewModal(false)}
        centered
        className="modal-width"
      >
        <Modal.Header closeButton>
          <Modal.Title>History Detail</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row">
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Category Title</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.historyData?.completion?.category_info?.title ?? "-"}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Category Name</div>
              <div className="col-md-7">
                {viewData?.historyData?.completion?.category_info
                  ?.translatedName ?? "-"}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Task Title</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.historyData?.completion?.task_info?.title ?? "-"}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Task Name</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.historyData?.completion?.task_info?.translatedName ??
                  "-"}
              </div>
            </div>
            <h3>Form Info</h3>
            {viewData?.historyData?.completion?.form_info?.fields_info?.map(
              (data) => (
                <>
                  <div className="row py-2">
                    <div className="col-md-5 fw-bold">Field Id</div>
                    <div className="col-md-7 text-capitalize">
                      {data?.field_id ?? "-"}
                    </div>
                  </div>
                  <div className="row py-2">
                    <div className="col-md-5 fw-bold">User Input</div>
                    <div className="col-md-7 text-capitalize">
                      {data?.user_input ?? "-"}
                    </div>
                  </div>
                </>
              )
            )}

            <h3>Model Responses</h3>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Content</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.historyData?.completion?.model_responses?.content ??
                  "-"}
              </div>
            </div>

            <h3>Chat Info</h3>
            <div className="row py-2">
              <div className="col-md-5 fw-bold"></div>
              <div className="col-md-7 text-capitalize">
                {viewData?.historyData?.chat ?? "-"}
              </div>
            </div>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button className="btn btn-color" onClick={() => setViewModal(false)}>
            Cancel
          </button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
