import React, { useEffect, useState } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import { useHistory } from "react-router-dom";
import Swal from "sweetalert2";
import SVG from "react-inlinesvg";
import NoDataTable from "../../../common/noDataTable";
import Pagination from "@material-ui/lab/Pagination";
import {
  ApiDelete,
  ApiGet,
  ApiPost,
  ApiPut,
} from "../../../helpers/API/ApiData";
import { ErrorToast, SuccessToast } from "../../../helpers/Toast";
import { toAbsoluteUrl } from "../../../_metronic/_helpers";
import moment from "moment";
import { Form, Modal } from "react-bootstrap";
import FormAddSectionButton from "../../components/Task/FormAddSectionButton/FormAddSectionButton";
import SingleInputField from "../../components/Task/SingleInputField/SingleInputField";
import MultipleInputField from "../../components/Task/MultipleInputField/MultipleInputField";
import DropdownField from "../../components/Task/DropdownField/DropdownField";
import DropdownOption from "../../components/Task/DropdownField/DropdownOption";
import addicon from "../../../../src/media/add_option.svg";
import RatingField from "../../components/Task/RatingField/RatingField";

export default function Questionnaire() {
  const history = useHistory();
  const [data, setData] = useState([]);
  const [modal, setModal] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [totalpage, settotalpage] = useState(0);
  const [currentpage, setcurrentpage] = useState(1);
  const [pagesize, setpagesize] = useState(10);
  const [searching, setsearching] = useState("");
  const [sorting, setSorting] = useState(1);
  const [createBtnBool, setCreateBtnBool] = useState(false);
  const [updateBtnBool, setUpdateBtnBool] = useState(false);
  const [deleteBtnBool, setDeleteBtnBool] = useState(false);
  const [singleInputs, setSingalInputs] = useState([]);
  const [multipleInput, setMultipleinput] = useState([]);
  const [dropdownInputs, setDropdownInputs] = useState([]);
  const [ratingInputs, setRatingInputs] = useState([]);
  const [arr, setArr] = useState([]);

  const onCloseHandler = () => {
    setModal(false);
    document.body.style.overflow = "auto";
  };

  const incrementInput = () => {
    setSorting((prevState) => prevState + 1);
  };

  const handleAddInput = (type, id) => {
    if (type === "singleInput") {
      if (singleInputs.length == 5) {
        return alert("limit reached");
      }

      incrementInput();
      const newIndex = singleInputs.length + 1;
      const newName = `SINGLE-INPUT-${newIndex}`;
      const newInput = {
        id: Date.now(),
        originalIndex: newIndex,
        name: newName,
        type: "input",
        placeholder: "",
        englishText: "",
        placeholder_ar: "",
        optional: false,
        sorting: sorting,
      };
      setSingalInputs([...singleInputs, newInput]);
    } else if (type === "multipleInput") {
      if (multipleInput.length == 5) {
        return alert("limit reached");
      }

      incrementInput();
      setMultipleinput([
        ...multipleInput,
        {
          id: Date.now(),
          name: `DYNAMIC-INPUT-${multipleInput.length + 1}`,
          type: "textarea",
          placeholder: "",
          englishText: "",
          placeholder_ar: "",
          optional: false,
          sorting: sorting,
        },
      ]);
    } else if (type === "dropdownInput") {
      if (dropdownInputs.length == 5) {
        return alert("limit reached");
      }

      incrementInput();
      setDropdownInputs([
        ...dropdownInputs,
        {
          id: Date.now(),
          name: `DROPDOWN-INPUT-${dropdownInputs.length + 1}`,
          type: "dropdown",
          placeholder: "",
          label: "",
          placeholder_ar: "",
          optional: false,
          sorting: sorting,
          options: [{ id: Date.now(), value: "", text: "", arabicText: "" }],
        },
      ]);
    } else if (type === "dropdownInputOption") {
      setDropdownInputs(
        dropdownInputs.map((input, index) => {
          if (index === id) {
            return {
              ...input,
              options: [
                ...input.options,
                { id: Date.now(), value: "", text: "", arabicText: "" },
              ],
            };
          } else {
            return input;
          }
        })
      );
    } else if (type === "ratingInput") {
      if (ratingInputs.length == 5) {
        return alert("limit reached");
      }

      incrementInput();
      const newIndex = ratingInputs.length + 1;
      const newName = `RATING-INPUT-${newIndex}`;
      const newInput = {
        id: Date.now(),
        originalIndex: newIndex,
        name: newName,
        type: "rating",
        isRating: false,
        placeholder: "",
        englishText: "",
        placeholder_ar: "",
        optional: false,
        sorting: sorting,
      };
      setRatingInputs([...ratingInputs, newInput]);
    }
  };

  const handleEnglishInputChange = (id, event, type, optionId) => {
    let updatedInputs = [];
    if (type === "singleInput") {
      updatedInputs = singleInputs.map((input) => {
        if (input.id === id) {
          return {
            ...input,
            placeholder: `${event.target.value}`,
            englishText: event.target.value,
          };
        }
        return input;
      });
      setSingalInputs(updatedInputs);
    } else if (type === "multipleInput") {
      updatedInputs = multipleInput.map((input) => {
        if (input.id === id) {
          return {
            ...input,
            placeholder: `${event.target.value}`,
            englishText: event.target.value,
          };
        }
        return input;
      });
      setMultipleinput(updatedInputs);
    } else if (type === "dropdownInput") {
      updatedInputs = dropdownInputs.map((input) => {
        if (input.id === id) {
          return { ...input, placeholder: event.target.value };
        }
        return input;
      });
      setDropdownInputs(updatedInputs);
    } else if (type === "dropdownOption") {
      updatedInputs = dropdownInputs.map((input) => {
        if (input.id === id) {
          const updatedOptions = input.options.map((option) => {
            if (option.id === optionId) {
              return {
                ...option,
                value: event.target.value,
                text: event.target.value,
              };
            }
            return option;
          });
          return { ...input, options: updatedOptions };
        }
        return input;
      });
      setDropdownInputs(updatedInputs);
    }
  };

  const handleArabicInputChange = (id, event, type, optionId) => {
    let updatedInputs = [];
    if (type === "singleInput") {
      updatedInputs = singleInputs.map((input) => {
        if (input.id === id) {
          return { ...input, placeholder_ar: event.target.value };
        }
        return input;
      });
      setSingalInputs(updatedInputs);
    } else if (type === "multipleInput") {
      updatedInputs = multipleInput.map((input) => {
        if (input.id === id) {
          return { ...input, placeholder_ar: event.target.value };
        }
        return input;
      });
      setMultipleinput(updatedInputs);
    } else if (type === "dropdownInput") {
      updatedInputs = dropdownInputs.map((input) => {
        if (input.id === id) {
          return { ...input, placeholder_ar: event.target.value };
        }
        return input;
      });
      setDropdownInputs(updatedInputs);
    } else if (type === "dropdownOption") {
      updatedInputs = dropdownInputs.map((input) => {
        if (input.id === id) {
          const updatedOptions = input.options.map((option) => {
            if (option.id === optionId) {
              return { ...option, arabicText: event.target.value };
            }
            return option;
          });
          return { ...input, options: updatedOptions };
        }
        return input;
      });
      setDropdownInputs(updatedInputs);
    }
  };

  const handleLabelInputChange = (id, event, type, optionId) => {
    let updatedInputs = [];
    if (type === "singleInput") {
      updatedInputs = singleInputs.map((input) => {
        if (input.id === id) {
          return { ...input, label: event.target.value };
        }
        return input;
      });
      setSingalInputs(updatedInputs);
    } else if (type === "multipleInput") {
      updatedInputs = multipleInput.map((input) => {
        if (input.id === id) {
          return { ...input, label: event.target.value };
        }
        return input;
      });
      setMultipleinput(updatedInputs);
    } else if (type === "dropdownInput") {
      updatedInputs = dropdownInputs.map((input) => {
        if (input.id === id) {
          return { ...input, label: event.target.value };
        }
        return input;
      });
      setDropdownInputs(updatedInputs);
    }
  };

  const handleRatingInputChange = (id, event, type) => {
    let updatedInputs = [];
    if (type === "ratingInput") {
      updatedInputs = ratingInputs.map((input) => {
        if (input.id === id) {
          return { ...input, label: event.target.value };
        }
        return input;
      });
      setRatingInputs(updatedInputs);
    }
  };

  const handleOptionalChange = (id, event, type) => {
    let updatedInputs = [];
    if (type === "singleInput") {
      updatedInputs = singleInputs.map((input) => {
        if (input.id === id) {
          return { ...input, optional: event.target.checked };
        }
        return input;
      });
      console.log(updatedInputs);
      setSingalInputs(updatedInputs);
    } else if (type === "multipleInput") {
      updatedInputs = multipleInput.map((input) => {
        if (input.id === id) {
          return { ...input, optional: event.target.checked };
        }
        return input;
      });
      setMultipleinput(updatedInputs);
    } else if (type === "dropdownInput") {
      updatedInputs = dropdownInputs.map((input) => {
        if (input.id === id) {
          return { ...input, optional: event.target.checked };
        }
        return input;
      });
      setDropdownInputs(updatedInputs);
    }
  };

  const handleDeleteInputs = (id, type, optionId) => {
    console.log("jghjhgjhgjhgjhjghjhgjhg");
    let updatedInputs = [];
    if (type === "singleInput") {
      // Filter out the input with the specified ID
      updatedInputs = arr.filter((input) => input.id !== id);

      // Update names for the remaining items to maintain sequence
      let singleInputCount = 1;
      updatedInputs = updatedInputs.map((input) => {
        if (input.name.toLowerCase().includes("single-input")) {
          return { ...input, name: `SINGLE-INPUT-${singleInputCount++}` };
        }
        return input;
      });

      setArr(updatedInputs);
      console.log(updatedInputs, "Updated Inputs");
    } else if (type === "multipleInput") {
      // Filter out the input with the specified ID
      updatedInputs = arr.filter((input) => input.id !== id);

      // Update names for the remaining items to maintain sequence
      let multipleInputCount = 1;
      updatedInputs = updatedInputs.map((input) => {
        if (input.name.toUpperCase().includes("MULTIPLE-INPUT")) {
          return { ...input, name: `MULTIPLE-INPUT-${multipleInputCount++}` };
        }
        return input;
      });

      setArr(updatedInputs);
    } else if (type === "dropdownInput") {
      // Filter out the input with the specified ID
      updatedInputs = arr.filter((input) => input.id !== id);

      // Update names for the remaining items to maintain sequence
      let dropdownInputCount = 1;
      updatedInputs = updatedInputs.map((input) => {
        if (input.name.toUpperCase().includes("DROPDOWN-INPUT")) {
          return { ...input, name: `DROPDOWN-INPUT-${dropdownInputCount++}` };
        }
        return input;
      });

      setArr(updatedInputs);
    } else if (type === "dropdownOption") {
      // Update options within the specified input
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          // Filter out the option with the specified ID
          const updatedOptions = input.options.filter(
            (option) => option.id !== optionId
          );

          // Update IDs for the remaining options
          const updatedOptionsWithNewIds = updatedOptions.map(
            (option, index) => ({
              ...option,
              id: `option-${index + 1}`,
            })
          );

          return { ...input, options: updatedOptionsWithNewIds };
        }
        return input;
      });

      setArr(updatedInputs);
    } else if (type === "ratingInput") {
      updatedInputs = arr.filter((input) => input.id !== id);

      let ratingInputCount = 1;
      updatedInputs = updatedInputs.map((input) => {
        if (input.name.toUpperCase().includes("RATING-INPUT")) {
          return { ...input, name: `RATING-INPUT-${ratingInputCount++}` };
        }
        return input;
      });

      setArr(updatedInputs);
    }
  };

  const handleDeleteInput = (id, type, optionId) => {
    console.log("dlelelelelleleleelelkel");
    let updatedInputs = [];
    if (type === "singleInput") {
      updatedInputs = singleInputs.filter((input) => input.id !== id);
      setSingalInputs(updatedInputs);
    } else if (type === "multipleInput") {
      updatedInputs = multipleInput.filter((input) => input.id !== id);
      setMultipleinput(updatedInputs);
    } else if (type === "dropdownInput") {
      updatedInputs = dropdownInputs.filter((input) => input.id !== id);
      setDropdownInputs(updatedInputs);
    } else if (type === "dropdownOption") {
      updatedInputs = dropdownInputs.map((input) => {
        if (input.id === id) {
          // Filter out the option with the specified ID
          const updatedOptions = input.options.filter(
            (option) => option.id !== optionId
          );
          return { ...input, options: updatedOptions };
        }
        return input;
      });
      setDropdownInputs(updatedInputs);
    } else if (type === "rating") {
      updatedInputs = ratingInputs.filter((input) => input.id !== id);
      setRatingInputs(updatedInputs);
    }
  };

  const handleAdd = (type, id) => {
    let inputType = "";
    let inputPrefix = "";

    switch (type) {
      case "singleInput":
        inputType = "input";
        inputPrefix = "SINGLE-INPUT";
        break;
      case "multipleInput":
        inputType = "textarea";
        inputPrefix = "MULTIPLE-INPUT";
        break;
      case "dropdownInput":
        inputType = "dropdown";
        inputPrefix = "DROPDOWN-INPUT";
        break;
      case "dropdownInputOption":
        inputType = "dropdownInputOption";
        inputPrefix = "DROPDOWN-INPUT";
        break;
      case "ratingInput":
        inputType = "rating";
        inputPrefix = "RATING-INPUT";
      default:
        break;
    }

    const inputCount = arr.filter((item) => item.type === inputType).length;
    if (inputCount >= 5 && inputType != "dropdownInputOption") {
      alert("Maximum limit reached");
      return;
    }

    incrementInput();
    if (inputType != "dropdownInputOption") {
      setArr((prevArr) => [
        ...prevArr,
        {
          id: Date.now(),
          name: `${inputPrefix}-${inputCount + 1}`,
          type: inputType,
          placeholder: "",
          englishText: "",
          placeholder_ar: "",
          optional: false,
          sorting: sorting,
          options:
            inputType === "dropdown"
              ? [{ id: Date.now(), value: "", text: "", arabicText: "" }]
              : [],
        },
      ]);
    } else {
      setArr(
        arr.map((input, index) => {
          // Check if the current input matches the ID
          console.log({ id });
          if (index === id) {
            return {
              ...input,
              options: [
                ...input.options,
                { id: Date.now(), value: "", text: "", arabicText: "" },
              ],
            };
          } else {
            // If not, return the input as is
            return input;
          }
        })
      );
    }
  };

  const handleEnglishInputChanges = (id, event, type, optionId) => {
    let updatedInputs = [];
    if (type === "singleInput") {
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          return {
            ...input,
            placeholder: `${event.target.value}`,
            englishText: event.target.value,
          };
        }
        return input;
      });
      setArr(updatedInputs);
    } else if (type === "multipleInput") {
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          return {
            ...input,
            placeholder: `${event.target.value}`,
            englishText: event.target.value,
          };
        }
        return input;
      });
      setArr(updatedInputs);
    } else if (type === "dropdownInput") {
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          return { ...input, placeholder: event.target.value };
        }
        return input;
      });
      setArr(updatedInputs);
    } else if (type === "dropdownOption") {
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          const updatedOptions = input.options.map((option) => {
            if (option.id === optionId) {
              return {
                ...option,
                value: event.target.value,
                text: event.target.value,
              };
            }
            return option;
          });
          return { ...input, options: updatedOptions };
        }
        return input;
      });
      setArr(updatedInputs);
    }
  };

  const handleArabicInputChanges = (id, event, type, optionId) => {
    let updatedInputs = [];
    if (type === "singleInput") {
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          return { ...input, placeholder_ar: event.target.value };
        }
        return input;
      });
      setArr(updatedInputs);
    } else if (type === "multipleInput") {
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          return { ...input, placeholder_ar: event.target.value };
        }
        return input;
      });
      setArr(updatedInputs);
    } else if (type === "dropdownInput") {
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          return { ...input, placeholder_ar: event.target.value };
        }
        return input;
      });
      setArr(updatedInputs);
    } else if (type === "dropdownOption") {
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          const updatedOptions = input.options.map((option) => {
            if (option.id === optionId) {
              return { ...option, arabicText: event.target.value };
            }
            return option;
          });
          return { ...input, options: updatedOptions };
        }
        return input;
      });
      setArr(updatedInputs);
    }
  };

  const handleLabelInputChanges = (id, event, type, optionId) => {
    let updatedInputs = [];
    if (type === "singleInput") {
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          return { ...input, label: event.target.value };
        }
        return input;
      });
      setArr(updatedInputs);
    } else if (type === "multipleInput") {
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          return { ...input, label: event.target.value };
        }
        return input;
      });
      setArr(updatedInputs);
    } else if (type === "dropdownInput") {
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          return { ...input, label: event.target.value };
        }
        return input;
      });
      setArr(updatedInputs);
    }
  };

  const handleRatingInputChanges = (id, event, type, optionId) => {
    let updatedInputs = [];
    if (type === "ratingInput") {
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          return { ...input, label: event.target.value };
        }
        return input;
      });
      setArr(updatedInputs);
    }
  };

  const handleOptionalChanges = (id, event, type) => {
    let updatedInputs = [];
    if (
      type === "singleInput" ||
      type === "multipleInput" ||
      type === "dropdownInput" ||
      type === "ratingInput"
    ) {
      updatedInputs = arr.map((input) => {
        if (input.id === id) {
          return { ...input, optional: event.target.checked };
        }
        return input;
      });
    }
    setArr(updatedInputs);
  };

  const columns = [
    {
      dataField: "_id",
      text: "ID",
      formatter: (cell, row, i) => {
        return (
          <div className="d-flex align-items-center">
            {(currentpage - 1) * pagesize + i + 1 || "-"}
          </div>
        );
      },
    },
    {
      dataField: "label",
      text: "Questions",
      sort: true,
      formatter: (cell, row) => {
        return <div>{cell || "-"}</div>;
      },
    },
    {
      dataField: "createdAt",
      text: "Created Date",
      sort: true,
      formatter: (cell, row) => {
        return <div>{moment(cell).format("DD-MM-YYYY") || "-"}</div>;
      },
    },
    {
      dataField: "action",
      text: "Action",
      sort: true,
      formatter: (cell, row) => {
        return (
          <>
            <a
              title="Edit customer"
              className="btn btn-icon btn-light btn-hover-primary btn-sm me-3"
              onClick={() => editBtnClick(row)}
            >
              <span className="svg-icon svg-icon-md svg-icon-primary">
                <SVG
                  src={toAbsoluteUrl(
                    "/media/svg/icons/Communication/Write.svg"
                  )}
                />
              </span>
            </a>

            <a
              title="Delete customer"
              className="btn btn-icon btn-light btn-hover-danger btn-sm"
              onClick={() => {
                Swal.fire({
                  text: `Are you sure you want to Delete ?`,
                  icon: "warning",
                  showCancelButton: true,
                  showConfirmButton: true,
                  confirmButtonText: `Yes, Delete`,
                  confirmButtonColor: "#D72852",
                  cancelButtonColor: "black",
                  cancelButtonText: "No, Cancel",
                }).then((res) => {
                  if (res.isConfirmed) {
                    if (!deleteBtnBool) {
                      onDelete(row?._id);
                    }
                  }
                });
              }}
            >
              <span className="svg-icon svg-icon-md svg-icon-danger">
                <SVG
                  src={toAbsoluteUrl("/media/svg/icons/General/Trash.svg")}
                />
              </span>
            </a>
            {/* <a onClick={() => hendleOnShorting(row)}>
              <span className="svg-icon svg-icon-md svg-icon-danger ml-4">
                Placeholder Sorting
              </span>
            </a> */}
          </>
        );
      },
    },
  ];

  const handleSubmit = async () => {
    setCreateBtnBool(true);
    const Input = arr.map((val) => {
      if (val.type === "rating") {
        return {
          ...val,
          isRating: true,
        };
      }

      return {
        id: val.id,
        name: val.name,
        label: val.label,
        type: val.type,
        isRating: val.isRating,
        placeholder: val.placeholder,
        englishText: val.englishText,
        placeholder_ar: val.placeholder_ar,
        optional: val.optional,
        sorting: val.sorting,
        options: val.options,
      };
    });

    const updatedBody = {
      form: Input,
    };

    console.log("submitbody", updatedBody);

    try {
      const response = await ApiPost("/questions", updatedBody);
      SuccessToast(response?.data?.message);
      setModal(false);
      fetchData();
      setCreateBtnBool(false);
    } catch (error) {
      ErrorToast(error?.message);
      setCreateBtnBool(false);
    }
  };

  const onDelete = async (Id) => {
    setDeleteBtnBool(true);
    await ApiDelete(`/question/${Id}`)
      .then(() => {
        Swal.fire({
          text: "You have Deleted Successfully!!!",
          icon: "success",
          confirmButtonText: "Ok, Got it!",
          confirmButtonColor: "#338DE6",
        });
        fetchData(currentpage, pagesize, searching);
        setDeleteBtnBool(false);
      })
      .catch((err) => {
        ErrorToast(err?.message);
        setDeleteBtnBool(false);
      });
  };

  const fetchData = async () => {
    try {
      const res = await ApiGet("/questions");
      setData(res?.data?.data);
    } catch (err) {
      ErrorToast(err?.message);
    }
  };

  const [currentRowId, setCurrentRowId] = useState(null);

  const editBtnClick = async (row) => {
    console.log("row", row);

    try {
      setArr([]);

      const res = await ApiGet(`/question/${row?._id}`);
      const question = res?.data?.data;

      if (!question) {
        throw new Error("No question found in the response");
      }
      const commonFields = {
        id: Date.now(),
        name: question.name,
        type: question.type,
        label: question.label,
        placeholder: question.placeholder,
        englishText: question.placeholder,
        placeholder_ar: question.placeholder_ar,
        optional: question.optional,
        sorting: question.sorting,
      };

      let additionalFields = {};

      if (question.type === "dropdown") {
        additionalFields = {
          options: question.option,
          label: question.placeholder,
        };
      }

      const field = { ...commonFields, ...additionalFields };
      setCurrentRowId(row?._id);
      setArr([field]);
      setIsEdit(true);
      setModal(true);
    } catch (err) {
      ErrorToast(err?.message);
    }
  };

  const onUpdate = async () => {
    setUpdateBtnBool(true);

    const Input = [];
    singleInputs.forEach((val) => Input.push(val));
    multipleInput.forEach((val) => Input.push(val));
    dropdownInputs.forEach((val) => Input.push(val));
    ratingInputs.forEach((val) => Input.push(val));
    arr.forEach((val) => Input.push(val));

    if (!currentRowId) {
      ErrorToast("Row ID is missing or invalid");
      setUpdateBtnBool(false);
      return;
    }

    const body = Input.map((input) => ({
      name: input.name,
      type: input.type,
      label: input.label,
      placeholder: input.placeholder,
      englishText: input.englishText,
      placeholder_ar: input.placeholder_ar,
      optional: input.optional,
      sorting: input.sorting,
      options: input.options,
    }));
    console.log("body======", body);

    try {
      const res = await ApiPut(`/question/${currentRowId}`, body[0]);

      SuccessToast(res?.data?.message || "Update successful!");
      setModal(false);
      fetchData();

      setSingalInputs([]);
      setMultipleinput([]);
      setDropdownInputs([]);
      setRatingInputs([]);
      setArr([]);
    } catch (err) {
      ErrorToast(err?.response?.data?.message || "An error occurred");
    } finally {
      setUpdateBtnBool(false);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  const handleonchnagespagination = (e) => {
    setpagesize(e.target.value);
    setcurrentpage(1);
    fetchData(1, parseInt(e.target.value), searching);
  };

  const onPaginationChange = (e, i) => {
    setcurrentpage(i);
    fetchData(i, pagesize, searching);
  };

  return (
    <>
      <div className="d-flex justify-content-between mb-4">
        <div className="title">
          <div className="fs-20px fw-bolder">Question List</div>
          <div>
            <span
              role="button"
              onClick={() => history.push("/dashboard")}
              className="text-hover-info text-muted"
            >
              Home
            </span>{" "}
            -{" "}
            <span className="text-muted" role="button">
              {" "}
              Question
            </span>
          </div>
        </div>
      </div>

      <div class="d-flex flex-column flex-column-fluid h-100" id="kt_content">
        <div class="card card-custom">
          <div class="card-header flex-wrap border-0 pt-6 pb-0 w-100">
            <div class="card-title"></div>

            <div class="card-toolbar">
              <a
                class="btn btn1 btn-color font-weight-bolder btn-sm ms-4"
                onClick={() => {
                  setArr([]);
                  setIsEdit(false);
                  setModal(true);
                }}
              >
                Add Question
              </a>
            </div>
          </div>

          <div className="card-body mb-5">
            <BootstrapTable
              wrapperClasses="table-responsive"
              headerWrapperClasses="border-0"
              bordered={false}
              classes="table table-head-custom table-vertical-center overflow-hidden"
              bootstrap4
              keyField="_id"
              data={data || []}
              columns={columns}
              noDataIndication={() => <NoDataTable />}
            />
            <div class="d-flex justify-content-between  pt-10">
              <div className="my-2">
                <Pagination
                  count={totalpage}
                  page={currentpage}
                  onChange={onPaginationChange}
                  variant="outlined"
                  shape="rounded"
                  className="pagination_"
                />
              </div>
              <div class="my-2">
                <div class="d-flex align-items-center pagination-drpdown">
                  <select
                    class="form-control pagination-drpdown1 dropdownPage"
                    id="kt_datatable_search_status"
                    onChange={(e) => handleonchnagespagination(e)}
                    value={pagesize}
                  >
                    <option value={10}>10</option>
                    <option value={20}>20</option>
                    <option value={30}>30</option>
                    <option value={50}>50</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Modal size="lg" show={modal} onHide={() => onCloseHandler()} centered>
        <Modal.Header closeButton>
          <Modal.Title>
            {!isEdit ? "Add Question" : "Question Edit Details"}
          </Modal.Title>
        </Modal.Header>

        <Modal.Body>
          <div className="row">
            {singleInputs.length > 0 &&
              singleInputs
                .slice()
                .sort((a, b) => {
                  const indexA = parseInt(a.name.split("-")[2]);
                  const indexB = parseInt(b.name.split("-")[2]);
                  return indexA - indexB;
                })
                .map((input, index) => (
                  <div key={input.id}>
                    <div
                      onClick={() =>
                        !isEdit && handleDeleteInput(input.id, "singleInput")
                      }
                      style={{ cursor: isEdit ? "not-allowed" : "pointer" }}
                    >
                      X
                    </div>
                    <SingleInputField
                      input={input}
                      handleEnglishInputChange={handleEnglishInputChange}
                      handleArabicInputChange={handleArabicInputChange}
                      handleLabelInputChange={handleLabelInputChange}
                      handleOptionalChange={handleOptionalChange}
                      handleDeleteInput={handleDeleteInput}
                      handleSetSingleInput={setSingalInputs}
                      singleInputs={singleInputs}
                      index={index}
                      showQuestionsField={true}
                    />
                  </div>
                ))}
            {/* {multipleInput.length > 0 &&
              multipleInput
                .slice()
                .sort((a, b) => {
                  const indexA = parseInt(a.name.split("-")[2]);
                  const indexB = parseInt(b.name.split("-")[2]);
                  return indexA - indexB;
                })
                .map((input, index) => (
                  <MultipleInputField
                    key={input.id}
                    input={input}
                    handleEnglishInputChange={handleEnglishInputChange}
                    handleArabicInputChange={handleArabicInputChange}
                    handleLabelInputChange={handleLabelInputChange}
                    handleOptionalChange={handleOptionalChange}
                    handleDeleteInput={handleDeleteInput}
                    index={index}
                    // disabled={isEdit}
                    showQuestionsField={true}
                  />
                ))} */}

            {dropdownInputs.length > 0 &&
              dropdownInputs
                .slice()
                .sort((a, b) => {
                  const indexA = parseInt(a.name.split("-")[2]);
                  const indexB = parseInt(b.name.split("-")[2]);
                  return indexA - indexB;
                })
                .map((input, index) => (
                  <div key={input.id}>
                    <DropdownField
                      input={input}
                      handleEnglishInputChange={handleEnglishInputChange}
                      handleArabicInputChange={handleArabicInputChange}
                      handleLabelInputChange={handleLabelInputChange}
                      handleOptionalChange={handleOptionalChange}
                      handleDeleteInput={handleDeleteInput}
                      index={index}
                      // disabled={isEdit}
                      showQuestionsField={true}
                    />
                    {input.options.map((option) => (
                      <DropdownOption
                        key={option.id}
                        input={option}
                        parentID={input.id}
                        handleEnglishInputChange={handleEnglishInputChange}
                        handleArabicInputChange={handleArabicInputChange}
                        handleOptionalChange={handleOptionalChange}
                        handleAddInput={handleAddInput}
                        handleDeleteInput={handleDeleteInput}
                        disabled={isEdit}
                      />
                    ))}

                    {!isEdit && (
                      <div
                        style={{
                          display: "flex",
                          justifyContent: "flex-end",
                          alignItems: "center",
                          position: "relative",
                        }}
                        onClick={() =>
                          handleAddInput("dropdownInputOption", index)
                        }
                      >
                        <div
                          style={{
                            position: "absolute",
                            right: "170px",
                            bottom: "3px",
                          }}
                        >
                          <img src={addicon} alt="mySvgImage" />{" "}
                          <span
                            style={{
                              color: "rgb(17, 93, 252)",
                              fontWeight: "bold",
                            }}
                          >
                            Add Options
                          </span>
                        </div>
                      </div>
                    )}
                  </div>
                ))}
            {ratingInputs.length > 0 &&
              ratingInputs
                .slice()
                .sort((a, b) => {
                  const indexA = parseInt(a.name.split("-")[2]);
                  const indexB = parseInt(b.name.split("-")[2]);
                  return indexA - indexB;
                })
                .map((input, index) => (
                  <div key={input.id}>
                    <div
                      onClick={() =>
                        !isEdit && handleDeleteInput(input.id, "ratingInput")
                      }
                      style={{ cursor: isEdit ? "not-allowed" : "pointer" }}
                    >
                      X
                    </div>
                    <RatingField
                      input={input}
                      handleRatingInputChange={handleRatingInputChange}
                      handleOptionalChange={handleOptionalChange}
                      handleDeleteInput={handleDeleteInput}
                      handleSetRatingInput={setRatingInputs}
                      ratingInputs={ratingInputs}
                      index={index}
                      showQuestionsField={true}
                    />
                  </div>
                ))}

            {arr.map((input, index) => (
              <div key={input.id}>
                {input.type === "input" && (
                  <SingleInputField
                    input={input}
                    handleEnglishInputChange={handleEnglishInputChanges}
                    handleArabicInputChange={handleArabicInputChanges}
                    handleLabelInputChange={handleLabelInputChanges}
                    handleOptionalChange={handleOptionalChanges}
                    handleDeleteInput={handleDeleteInputs}
                    handleSetSingleInput={setSingalInputs}
                    singleInputs={singleInputs}
                    index={index}
                    // disabled={isEdit}
                    showQuestionsField={true}
                    // onClick={() => handleRatingClick(input._id)}
                  />
                )}

                {/* {input.type === "textarea" && (
                  <MultipleInputField
                    input={input}
                    handleEnglishInputChange={handleEnglishInputChanges}
                    handleArabicInputChange={handleArabicInputChanges}
                    handleLabelInputChange={handleLabelInputChanges}
                    handleOptionalChange={handleOptionalChanges}
                    handleDeleteInput={handleDeleteInputs}
                    handleSetMultipleInput={setMultipleinput}
                    multipleInput={multipleInput}
                    index={index}
                    // disabled={isEdit}
                    showQuestionsField={true}
                  />
                )} */}

                {input.type === "dropdown" && (
                  <div>
                    <DropdownField
                      input={input}
                      handleEnglishInputChange={handleEnglishInputChanges}
                      handleArabicInputChange={handleArabicInputChanges}
                      handleLabelInputChange={handleLabelInputChanges}
                      handleOptionalChange={handleOptionalChanges}
                      handleDeleteInput={handleDeleteInputs}
                      handleSetDropdownInput={setDropdownInputs}
                      dropdownInput={dropdownInputs}
                      index={index}
                      // disabled={isEdit}
                      showQuestionsField={true}
                    />
                    {input.options.map((option) => (
                      <DropdownOption
                        key={option.id}
                        input={option}
                        parentID={input.id}
                        handleEnglishInputChange={handleEnglishInputChanges}
                        handleArabicInputChange={handleArabicInputChanges}
                        handleOptionalChange={handleOptionalChanges}
                        handleAddInput={handleAdd}
                        handleDeleteInput={handleDeleteInputs}
                        disabled={isEdit}
                      />
                    ))}
                    {!isEdit && (
                      <div
                        style={{
                          display: "flex",
                          justifyContent: "flex-end",
                          alignItems: "center",
                          position: "relative",
                        }}
                        onClick={() => handleAdd("dropdownInputOption", index)}
                      >
                        <div
                          style={{
                            position: "absolute",
                            right: "113px",
                            bottom: "3px",
                          }}
                        >
                          <img src={addicon} alt="mySvgImage" />{" "}
                          <span
                            style={{
                              color: "rgb(17, 93, 252)",
                              fontWeight: "bold",
                            }}
                          >
                            Add Options
                          </span>
                        </div>
                      </div>
                    )}
                  </div>
                )}
                {input.type === "rating" && (
                  <RatingField
                    input={input}
                    handleRatingInputChange={handleRatingInputChanges}
                    handleOptionalChange={handleOptionalChanges}
                    handleDeleteInput={handleDeleteInputs}
                    handleSetRatingInput={setRatingInputs}
                    index={index}
                    showQuestionsField={true}
                  />
                )}
              </div>
            ))}
            {!isEdit && (
              <FormAddSectionButton
                handleAddInput={handleAdd}
                showLengthForm={false}
              />
            )}
          </div>
        </Modal.Body>

        <Modal.Footer>
          {isEdit ? (
            <button
              className="btn btn1 btn-color"
              disabled={updateBtnBool}
              onClick={onUpdate}
            >
              Update Changes
            </button>
          ) : (
            <button
              disabled={createBtnBool}
              className="btn btn1 btn-color"
              onClick={handleSubmit}
            >
              Submit
            </button>
          )}
        </Modal.Footer>
      </Modal>
    </>
  );
}
