import React, { useState } from "react";
import { Form, FormControl, Button } from "react-bootstrap";
import crossicon from "../../../../../src/media/delete_option.svg";

const RatingField = (props) => {
  return (
    <div
      className="col-md-12"
      key={props.input.id}
      style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between",
        gap: "3rem",
        position: "relative",
      }}
    >
      <Form.Group controlId="english" style={{ width: "57%" }}>
        <Form.Label>Questions:</Form.Label>
        <FormControl
          type="text"
          placeholder="Enter Your Questions"
          value={props.input.label}
          onChange={(event) =>
            props.handleRatingInputChange(props.input.id, event, "ratingInput")
          }
        />
      </Form.Group>

      <div
        onClick={() => props.handleDeleteInput(props.input.id, "ratingInput")}
      >
        <img src={crossicon} alt="mySvgImage" />
      </div>
      <hr />
    </div>
  );
};

export default RatingField;
