import React, { useEffect, useRef, useState } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import { useHistory } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import Swal from "sweetalert2";
import SVG from "react-inlinesvg";
import NoDataTable from "../../../../common/noDataTable";
import Pagination from "@material-ui/lab/Pagination";
import {
  ApiDelete,
  ApiGet,
  ApiPatch,
  ApiPost,
  ApiPut,
  ApiUpload,
  Bucket,
} from "../../../../helpers/API/ApiData";
import { ErrorToast, SuccessToast } from "../../../../helpers/Toast";
import { toAbsoluteUrl } from "../../../../_metronic/_helpers";
import moment from "moment";
import {Form, Modal } from "react-bootstrap";
import { MdCancel } from "react-icons/md";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";

export default function Categories() {
  const date = new Date();
  const fileInputRef = useRef(null);
  const history = useHistory();
  const [data, setData] = useState([]);
  const [modal, setModal] = useState(false);
  const [viewModal, setViewModal] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [totalpage, settotalpage] = useState(0);
  const [currentpage, setcurrentpage] = useState(1); 
  const [pagesize, setpagesize] = useState(10);
  const [searching, setsearching] = useState("");
  const [countryData, setCountryData] = useState([]);
  const [error, setError] = useState("");
  const [shortingModal, setShortingModal] = useState(false);
  const [shortingData, setShortingData] = useState([]);
  const [categoriesShortingModal, setCategoriesShortingModal] = useState(false);
  const [categoriesShortingData, setCategoriesShortingData] = useState([]);

  const [newData, setNewData] = useState({
    image: "",
    categories: "",
    translatName: "",
    description: "",
    translatDescription: "",
    contact: "",
    date: moment(date).format("YYYY-MM-DD"),
    isfavorite: false,
    // country: "",
  });
  const [viewData, setViewData] = useState({});

  const columns = [
    {
      dataField: "_id",
      text: "ID",
      // sort: true,
      formatter: (cell, row, i) => {
        return (
          <div className="d-flex align-items-center">
            {(currentpage - 1) * pagesize + i + 1 || "-"}
          </div>
        );
      },
    },
    {
      dataField: "image",
      text: "Image",
      sort: true,
      headerStyle: {
        textAlign: "center",
      },
      style: {
        textAlign: "center",
      },
      formatter: (cell, row) => {
        return (
          <div
            className="cursor-pointer"
            //    onClick={() => viewBtnClick(row)}
          >
            {/* <img src={row?.image} alt="loading.." srcset="" /> */}
            <img
              src={
                row?.image
                  ? `${Bucket}${row?.image}`
                  : "https://static.vecteezy.com/system/resources/thumbnails/014/066/812/small/discount-ribbon-banner-icon-for-graphic-design-logo-website-social-media-mobile-app-ui-illustration-vector.jpg"
              }
              alt=""
              width={50}
              height={50}
              className="rounded object-cover"
            />
          </div>
        );
      },
    },
    // {
    //   dataField: "Categories",
    //   text: "Categories",
    //   sort: true,
    //   formatter: (cell, row) => {
    //     return <div>{cell || "-"}</div>;
    //   },
    // },
    {
      dataField: "CategoriesName",
      text: "Categories Name",
      sort: true,
      headerStyle: {
        textAlign: "center",
      },
      style: {
        textAlign: "center",
      },
      formatter: (cell, row) => {
        return <div>{row?.name ? row?.name : "-"}</div>;
      },
    },
    {
      dataField: "Description",
      text: "Description",
      sort: true,
      headerStyle: {
        textAlign: "center",
      },
      style: {
        textAlign: "center",
      },
      formatter: (cell, row) => {
        const descriptionStyle = {
          width: "200px", // Replace with your desired width
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
          cursor: "pointer",
        };
        return (
          <div
          // onClick={() => viewBtnClick(row)}
          // style={descriptionStyle}
          >
            {row?.descripiton ? row?.descripiton : "-"}
          </div>
        );
      },
    },
    {
      dataField: "Favorite",
      text: "Favorite Categories",
      sort: true,
      headerStyle: {
        textAlign: "center",
      },
      style: {
        textAlign: "center",
      },
      formatter: (cell, row) => {
        return (
          <div>
            {row?.isfavorite ? (
              <svg
                width="16"
                height="16"
                className="me-2 pointer"
                viewBox="0 0 25 23"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M0.255859 7.28189C0.255556 5.5423 0.966817 3.87563 2.22962 2.65689C3.49241 1.43814 5.20069 0.769679 6.97009 0.801894C9.06654 0.79095 11.067 1.6649 12.4635 3.20189C13.8601 1.6649 15.8606 0.79095 17.957 0.801894C19.7264 0.769679 21.4347 1.43814 22.6975 2.65689C23.9603 3.87563 24.6715 5.5423 24.6712 7.28189C24.6712 13.7091 16.884 18.5619 12.4635 22.4019C8.05291 18.5295 0.255859 13.7139 0.255859 7.28189Z"
                  fill="#ED3628"
                />
              </svg>
            ) : (
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                className="bi bi-heart me-2 pointer"
                viewBox="0 0 16 16"
              >
                <path d="m8 2.748-.717-.737C5.6.281 2.514.878 1.4 3.053c-.523 1.023-.641 2.5.314 4.385.92 1.815 2.834 3.989 6.286 6.357 3.452-2.368 5.365-4.542 6.286-6.357.955-1.886.838-3.362.314-4.385C13.486.878 10.4.28 8.717 2.01L8 2.748zM8 15C-7.333 4.868 3.279-3.04 7.824 1.143c.06.055.119.112.176.171a3.12 3.12 0 0 1 .176-.17C12.72-3.042 23.333 4.867 8 15z" />
              </svg>
            )}
          </div>
        );
      },
    },
    // {
    //   dataField: "gender",
    //   text: "Gender",
    //   sort: true,
    //   formatter: (cell, row) => {
    //     return <div>{row?.gender === 0 ? "Male" : "Female"}</div>;
    //   },
    // },
    // {
    //   dataField: "dob",
    //   text: "DOB",
    //   sort: true,
    //   formatter: (cell, row) => {
    //     return (
    //       <div>{cell ? moment(cell).format("DD-MM-YYYY") : "-" || "-"}</div>
    //     );
    //   },
    // },
    {
      dataField: "createdAt",
      text: "Created Date",
      sort: true,
      headerStyle: {
        textAlign: "center",
      },
      style: {
        textAlign: "center",
      },
      formatter: (cell, row) => {
        return <div>{moment(cell).format("DD-MM-YYYY") || "-"}</div>;
      },
    },
    {
      dataField: "action",
      text: "Action",
      sort: true,
      headerStyle: {
        textAlign: "center",
      },
      formatter: (cell, row) => {
        return (
          <>
            {/* <a
              title="View User"
              className="btn btn-icon btn-light btn-hover-primary btn-sm me-3"
              onClick={() => viewBtnClick(row)}
            >
              <span className="svg-icon svg-icon-md svg-icon-primary eye-icon">
                <AiFillEye className="" />
              </span>
            </a> */}
            {/* <div className="d-flex btn-center"> */}
            <a
              title="Edit customer"
              className="btn btn-icon btn-light btn-hover-primary btn-sm me-3"
              onClick={() => editBtnClick(row)}
            >
              <span className="svg-icon svg-icon-md svg-icon-primary">
                <SVG
                  src={toAbsoluteUrl(
                    "/media/svg/icons/Communication/Write.svg"
                  )}
                />
              </span>
            </a>

            <a
              title="Delete customer"
              className="btn btn-icon btn-light btn-hover-danger btn-sm"
              onClick={() => {
                Swal.fire({
                  text: `Are you sure you want to Delete ?`,
                  icon: "warning",
                  showCancelButton: true,
                  showConfirmButton: true,
                  confirmButtonText: `Yes, Delete`,
                  confirmButtonColor: "#D72852",
                  cancelButtonColor: "black",
                  cancelButtonText: "No, Cancel",
                }).then((res) => {
                  if (res.isConfirmed) {
                    onDelete(row?._id);
                  }
                });
              }}
            >
              <span className="svg-icon svg-icon-md svg-icon-danger">
                <SVG
                  src={toAbsoluteUrl("/media/svg/icons/General/Trash.svg")}
                />
              </span>
            </a>
            <a onClick={() => hendleOnShorting(row)}>
              <span className="svg-icon svg-icon-md svg-icon-danger ml-4">
                Task Sorting
              </span>
            </a>
            {/* </div> */}
          </>
        );
      },
    },
  ];

  const handleChange = (e) => {
    const { value, name, checked } = e.target;
    if (name === "isfavorite") {
      setNewData({ ...newData, [name]: checked });
    } else {
      setNewData({ ...newData, [name]: value });
    }
  };
  console.log("newData", newData);

  const editBtnClick = (row) => {
    console.log("row123", row);
    setIsEdit(true);
    setModal(true);
    setNewData({
      id: row?._id,
      image: row?.image,
      categories: row?.name,
      translatName: row?.translatName,
      description: row?.descripiton,
      translatDescription: row?.translatDescription,
      contact: row?.phoneNumber,
      date: moment(row?.createdAt).format("YYYY-MM-DD"),
      isfavorite: row?.isfavorite,
      // country: row?.country,
    });
  };

  //shorting button onClick
  const hendleOnShorting = async (row) => {
    setShortingModal(true);
    await ApiGet(`/getTask/${row?._id}`)
      .then((res) => {
        console.log("res", res);
        setShortingData(res?.data?.data);
      })
      .catch((err) => {
        console.log("err", err);
      });
  };

  const hendleOnCategoryShoting = async () => {
    setCategoriesShortingModal(true);
    await ApiGet(`/category`)
      .then((res) => {
        console.log("categories res =", res);
        setCategoriesShortingData(res?.data?.data);
      })
      .catch((err) => {
        console.log("err", err);
      });
  };

  //drag and drop click
  const onDragEnd = (result) => {
    if (!result.destination) return;

    const items = Array.from(shortingData);
    const [reorderedItem] = items.splice(result.source.index, 1);
    items.splice(result.destination.index, 0, reorderedItem);

    setShortingData(items);
  };

  const onDragEndCategorie = (result) => {
    if (!result.destination) return;

    const items = Array.from(categoriesShortingData);
    const [reorderedItem] = items.splice(result.source.index, 1);
    items.splice(result.destination.index, 0, reorderedItem);

    setCategoriesShortingData(items);
  };

  //save click for drag and drop updated data
  const saveOnShorting = async () => {
    const body = {
      tasks: shortingData?.map((item) => item?._id),
    };
    await ApiPatch(`/task/updateOrder`, body)
      .then((res) => {
        console.log("res", res);
        SuccessToast(res?.data?.message);
        setShortingModal(false);
        setShortingData([]);
      })
      .catch((err) => {
        console.log("err", err);
        ErrorToast(err?.message);
      });
  };

  const saveCategoriesShorting = async () => {
    const body = {
      categories: categoriesShortingData?.map((item) => item?._id),
    };
    await ApiPatch(`/category/updateOrder`, body)
      .then((res) => {
        console.log("CategoriesShorting res", res);
        SuccessToast(res?.data?.message);
        setCategoriesShortingModal(false);
        setCategoriesShortingData([]);
      })
      .catch((err) => {
        console.log("err", err);
        ErrorToast(err?.message);
      });
  };

  const viewBtnClick = (row) => {
    // setIsEdit(true);
    setViewModal(true);
    setViewData(row);
    setNewData({
      id: row?._id,
      image: row?.image,
      categories: row?.name,
      description: row?.descripiton,
      contact: row?.phoneNumber,
      date: moment(row?.createdAt).format("YYYY-MM-DD"),
      isfavorite: row?.isfavorite,
      // date: moment(row?.date).format("YYYY-MM-DD"),
      // country: row?.country,
    });
  };
  const onDelete = async (Id) => {
    await ApiDelete(`/category/${Id}`)
      .then(() => {
        Swal.fire({
          text: "You have Deleted Successfully!!!",
          icon: "success",
          confirmButtonText: "Ok, Got it!",
          confirmButtonColor: "#338DE6",
        });
        fetchData(currentpage, pagesize, searching);
      })
      .catch((err) => ErrorToast(err?.message));
  };
  const onUpdate = async () => {
    let body = {
      id: newData?.id,
      // firstName: newData?.firstName,
      image: newData?.image,
      name: newData?.categories,
      translatName: newData?.translatName,
      descripiton: newData?.description,
      translatDescription: newData?.translatDescription,
      isfavorite: newData?.isfavorite,
      createdAt: moment(newData?.date).format("MM-DD-YYYY"),
    };

    console.log("body", body);

    if (
      newData?.image &&
      newData?.categories.trim().length > 0 &&
      newData?.description.trim().length > 0
    ) {
      await ApiPut("/category", body)
        .then((res) => {
          SuccessToast(res?.data?.message);
          setModal(false);
          fetchData(currentpage, pagesize, searching);
        })
        .catch((err) => ErrorToast(err?.message));
    } else if (!newData?.image) {
      ErrorToast("Image is Required!");
    } else if (
      !newData?.categories ||
      newData?.categories.trim().length === 0
    ) {
      ErrorToast("Category is Required!");
    } else if (
      !newData?.description ||
      newData?.description.trim().length === 0
    ) {
      ErrorToast("Description is Required!");
    }
  };
  const handleSubmit = async () => {
    let body = {
      // firstName: newData?.firstName,
      image: newData?.image,
      name: newData?.categories,
      translatName: newData?.translatName,
      descripiton: newData?.description,
      translatDescription: newData?.translatDescription,
      isfavorite: newData?.isfavorite,
    };
    if (
      newData?.image &&
      newData?.categories.trim().length > 0 &&
      newData?.description.trim().length > 0
    ) {
      await ApiPost("/category", body)
        .then((res) => {
          SuccessToast(res?.data?.message);
          setModal(false);
          fetchData(currentpage, pagesize, searching);
        })
        .catch((err) => ErrorToast(err?.message));
    } else if (!newData?.image) {
      ErrorToast("Image is Required!");
    } else if (
      !newData?.categories ||
      newData?.categories.trim().length === 0
    ) {
      ErrorToast("Category is Required!");
    } else if (
      !newData?.description ||
      newData?.description.trim().length === 0
    ) {
      ErrorToast("Description is Required!");
    }
  };
  const handleonchnagespagination = (e) => {
    setpagesize(e.target.value);
    setcurrentpage(1);
    fetchData(1, parseInt(e.target.value), searching);
  };
  const onPaginationChange = (e, i) => {
    setcurrentpage(i);
    fetchData(i, pagesize, searching);
  };
  // const imageChange = async (e) => {
  //   let file = e.target.files[0];
  //   let fileURL = URL.createObjectURL(file);
  //   file.fileURL = fileURL;
  //   let formData = new FormData();
  //   formData.append("image", file);
  //   await ApiUpload("admin/image", formData)
  //     .then((res) => {
  //       console.log('res', res)
  //       setNewData({ ...newData, image: res?.data?.data });
  //     })
  //     .catch((err) => console.log("res_blob", err));
  // };
  const imageChange = async (e) => {
    let file = e.target.files[0];
    if (file.type !== "image/svg+xml") {
      ErrorToast("Please upload only SVG files.");
      if (fileInputRef.current) {
        fileInputRef.current.value = "";
      }
      return;
    }
    let fileURL = URL.createObjectURL(file);
    let img = new Image();
    img.src = fileURL;

    img.onload = async () => {
      const svgWidth = img.width;
      const svgHeight = img.height;
      if (svgWidth <= 50 && svgHeight <= 50) {
        let formData = new FormData();
        formData.append("image", file);

        await ApiUpload("admin/image", formData)
          .then((res) => {
            setNewData({ ...newData, image: res?.data?.data });
          })
          .catch((err) => console.log("res_blob", err));
      } else {
        if (fileInputRef.current) {
          fileInputRef.current.value = "";
        }
        ErrorToast("Please select valid image dimension.(Max Size:50*50)");
      }
    };
  };

  const handlesearch = (e) => {
    setcurrentpage(1);
    setsearching(e.target.value);
    fetchData(1, pagesize, e.target.value);
  };

  const getCountry = async () => {
    let body = {
      page: 1,
      limit: 50,
      search: "",
    };
    await ApiPost("/get/country", body)
      .then((res) => {
        setCountryData(res?.data?.data?.response);
      })
      .catch((err) => ErrorToast(err?.message));
  };
  const fetchData = async (page, limit, search) => {
    let body = { page, limit, search };
    await ApiPost("/getcategory", body)
      .then((res) => {
        console.log("/workout/get", res?.data?.data);
        setData(res?.data?.data?.response);
        settotalpage(res?.data?.data?.state?.page_limit);
      })
      .catch((err) => ErrorToast(err?.message));
  };
  useEffect(() => {
    fetchData(currentpage, pagesize, searching);
    // getCountry();
  }, []);

  console.log("${Bucket}${newData?.image}", ` ${Bucket}${newData?.image}`);

  return (
    <>
      <div className="d-flex justify-content-between mb-4">
        <div className="title">
          <div className="fs-20px fw-bolder">Categories List</div>
          <div>
            <span
              role="button"
              onClick={() => history.push("/dashboard")}
              className="text-hover-info text-muted"
            >
              Home
            </span>{" "}
            -{" "}
            <span className="text-muted" role="button">
              {" "}
              Categories
            </span>
          </div>
        </div>
      </div>
      <div class="d-flex flex-column flex-column-fluid h-100" id="kt_content">
        <div class="card card-custom">
          <div class="card-header flex-wrap border-0 pt-6 pb-0 w-100">
            <div class="card-title ">
              {/* <h3 class="card-label">Video Playlist</h3> */}
              <div class="input-icon">
                <input
                  type="text"
                  class="form-control bg-light"
                  name="searchText"
                  placeholder="Search by Categories"
                  value={searching}
                  onChange={(e) => handlesearch(e)}
                  id="kt_datatable_search_query"
                />
                <span>
                  <i class="flaticon2-search-1 text-muted"></i>
                </span>
              </div>
            </div>

            <div className="d-flex">
              <div class="card-toolbar">
                <a
                  class="btn btn1 btn-color  font-weight-bolder btn-sm ms-4"
                  onClick={() => {
                    hendleOnCategoryShoting();
                    // setIsEdit(false);
                    // setModal(true);
                    // setNewData({
                    //   image: "",
                    //   categories: "",
                    //   description: "",
                    //   contact: "",
                    //   date: moment(date).format("YYYY-MM-DD"),
                    //   isfavorite: false,
                    // });
                  }}
                >
                  Sort Categories
                </a>
              </div>
              <div class="card-toolbar">
                <a
                  class="btn btn1 btn-color  font-weight-bolder btn-sm ms-4"
                  onClick={() => {
                    setIsEdit(false);
                    setModal(true);
                    setNewData({
                      image: "",
                      categories: "",
                      description: "",
                      contact: "",
                      date: moment(date).format("YYYY-MM-DD"),
                      isfavorite: false,
                    });
                  }}
                >
                  Add Categories
                </a>
              </div>
            </div>
          </div>
          <div className="card-body mb-5">
            {console.log(data, "12344")}
            <BootstrapTable
              wrapperClasses="table-responsive"
              headerWrapperClasses="border-0"
              bordered={false}
              classes="table table-head-custom table-vertical-center overflow-hidden"
              bootstrap4
              keyField="_id"
              // selectRow={selectRow}
              data={data || []}
              columns={columns}
              // pagination={paginationFactory(options)}
              // defaultSorted={defaultSorted}
              noDataIndication={() => <NoDataTable />}
              // filter={filterFactory()}
            />
            <div class="d-flex justify-content-between  pt-10">
              <div className="my-2">
                <Pagination
                  count={totalpage}
                  page={currentpage}
                  onChange={onPaginationChange}
                  variant="outlined"
                  shape="rounded"
                  className="pagination_"
                />
              </div>
              <div class="my-2">
                <div class="d-flex align-items-center pagination-drpdown">
                  <select
                    class="form-control pagination-drpdown1 dropdownPage"
                    id="kt_datatable_search_status"
                    onChange={(e) => handleonchnagespagination(e)}
                    value={pagesize}
                  >
                    <option value={10}>10</option>
                    <option value={20}>20</option>
                    <option value={30}>30</option>
                    <option value={50}>50</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Add Categories Model */}
      <Modal show={modal} onHide={() => setModal(false)} centered>
        <Modal.Header closeButton>
          <Modal.Title>
            {" "}
            {!isEdit ? " Add Categories" : "Categories Edit Details"}{" "}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row">
            <Form.Group className="col-md-12">
              <Form.Label>Image</Form.Label>
              <input
                ref={fileInputRef}
                type="file"
                className="form-control"
                name="image"
                id="image"
                onChange={imageChange}
              />
              <span className="errorInput"></span>
            </Form.Group>
            {newData?.image && (
              <div className="position-relative w-auto">
                <img
                  src={
                    newData?.image
                      ? `${Bucket}${newData?.image}`
                      : "https://static.vecteezy.com/system/resources/thumbnails/014/066/812/small/discount-ribbon-banner-icon-for-graphic-design-logo-website-social-media-mobile-app-ui-illustration-vector.jpg"
                  }
                  alt="sd"
                  style={{ width: "100px", height: "100px" }}
                  className="rounded-3"
                />
                <span
                  className="imageRemovoIconSpan"
                  onClick={() => {
                    setNewData({ ...newData, image: "" });
                    document.getElementById("image").value = null;
                  }}
                >
                  <MdCancel color="red" fontSize={20} />
                </span>
              </div>
            )}

            <Form.Group className="col-md-12">
              <Form.Label>Categories</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter Categories"
                label="categories"
                id="categories"
                required
                name="categories"
                onChange={handleChange}
                value={newData.categories}
              />
              <span className="errorInput">
                {/* {newData.categories?.length > 0 ? "" : errors["categories"]} */}
                {/* {newData.categories?.length > 0 ? "" : ErrorToast("Enter categories")} */}
              </span>
            </Form.Group>
            <Form.Group className="col-md-12">
              <Form.Label>Categories (AR)</Form.Label>
              <Form.Control
                type="text"
                placeholder="أدخل الفئات"
                label="translatName"
                id="translatName"
                required
                name="translatName"
                style={{ textAlign: "right" }}
                onChange={handleChange}
                value={newData.translatName}
              />
              <span className="errorInput">
                {/* {newData.categories?.length > 0 ? "" : errors["categories"]} */}
                {/* {newData.categories?.length > 0 ? "" : ErrorToast("Enter categories")} */}
              </span>
            </Form.Group>
            <Form.Group className="col-md-12">
              <Form.Label>Favorite</Form.Label>
              <Form.Check
                type="checkbox"
                label="Add Categories is your favorite"
                id="favoriteCategoriesCheckbox"
                checked={newData.isfavorite}
                name="isfavorite"
                onChange={handleChange}
              />
            </Form.Group>

            <Form.Group className="col-md-12">
              <Form.Label>Description</Form.Label>
              <Form.Control
                // type="text"
                // placeholder="Enter Description"
                // label="description"
                // id="description"
                // required
                // name="description"
                // onChange={handleChange}
                // value={newData.description}
                as="textarea"
                rows={3}
                placeholder="Enter Description"
                label="description"
                id="description"
                required
                name="description"
                onChange={handleChange}
                value={newData.description}
                style={{ whiteSpace: "pre-wrap" }}
              />
              <span className="errorInput">
                {/* {newData.description?.length > 0 ? "" : errors["name"]} */}
              </span>
            </Form.Group>
            <Form.Group className="col-md-12">
              <Form.Label>Description (AR)</Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                placeholder="أدخل الوصف"
                label="translatDescription"
                id="translatDescription"
                required
                name="translatDescription"
                onChange={handleChange}
                value={newData.translatDescription}
                style={{ whiteSpace: "pre-wrap", textAlign: "right" }}
              />
              <span className="errorInput">
                {/* {newData.description?.length > 0 ? "" : errors["name"]} */}
              </span>
            </Form.Group>

            {/* <Form.Group className="col-md-12">
              <Form.Label>Contact No.</Form.Label>
              <Form.Control
                type="number"
                placeholder="Enter contact no"
                label="contact"
                id="contact"
                required
                name="contact"
                onChange={handleChange}
                value={newData.contact}
              />
              <span className="errorInput">
             
              </span>
            </Form.Group> */}

            {/* <Form.Group className="col-md-12">
              <Form.Label>Country</Form.Label>
              <Form.Control
                as="select"
                custom
                id="country"
                required
                className="form-control"
                name="country"
                onChange={handleChange}
                value={newData.country}
              >
                <option value="" disabled>
                  Select country
                </option>
                {countryData?.map((v, i) => {
                  return (
                    <>
                      <option value={v?.countryName}>{v?.countryName}</option>
                    </>
                  );
                })}
              </Form.Control>
            </Form.Group> */}
          </div>
        </Modal.Body>
        <Modal.Footer>
          {isEdit ? (
            <button className="btn btn1 btn-color" onClick={onUpdate}>
              Update Changes
            </button>
          ) : (
            <button className="btn btn1 btn-color" onClick={handleSubmit}>
              Submit
            </button>
          )}
        </Modal.Footer>
      </Modal>

      <Modal
        show={viewModal}
        onHide={() => setViewModal(false)}
        centered
        className="modal-width"
      >
        <Modal.Header closeButton>
          <Modal.Title>Categories Details</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row">
            <div className="text-center" style={{ marginBottom: "1.75rem" }}>
              <img
                src={
                  viewData?.image
                    ? `${Bucket}${viewData?.image}`
                    : "https://static.vecteezy.com/system/resources/thumbnails/014/066/812/small/discount-ribbon-banner-icon-for-graphic-design-logo-website-social-media-mobile-app-ui-illustration-vector.jpg"
                }
                onError={({ currentTarget }) => {
                  currentTarget.onerror = null;
                  currentTarget.src =
                    "https://cdn.icon-icons.com/icons2/1378/PNG/512/avatardefault_92824.png";
                }}
                alt="userImage"
                className=" rounded-pill object-cover"
                height="100px"
                width="100px"
              />
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Category Name</div>
              <div className="col-md-7 text-capitalize">
                {newData?.categories ?? "-"}
              </div>
            </div>

            <div className="row py-2">
              <div className="col-md-5 fw-bold">Description </div>
              <div className="col-md-7">{newData?.description ?? "-"}</div>
            </div>

            {/* <div className="row py-2">
              <div className="col-md-5 fw-bold">Email</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.email ?? "-"}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">DOB</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.dob
                  ? moment(viewData?.createdAt).format("DD-MM-YYYY")
                  : "-"}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Gender</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.gender === 0 ? "Male" : "Female"}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">About Me</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.aboutMe ? viewData?.aboutMe : "-"}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Hobby</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.hobby ? viewData?.hobby : "-"}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Relationship Status </div>
              <div className="col-md-7 text-capitalize">
                {viewData?.relationshipStatus
                  ? viewData?.relationshipStatus === 0
                    ? "Married "
                    : viewData?.relationshipStatus === 1
                      ? "Single"
                      : "Relationship"
                  : "-"}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Shoe Size</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.size?.ShoeSize ? viewData?.size?.ShoeSize : "-"}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Bust</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.size?.Bust ? viewData?.size?.Bust : "-"}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Shirt Size</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.size?.ShirtSize ? viewData?.size?.ShirtSize : "-"}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Dress</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.size?.Dress ? viewData?.size?.Dress : "-"}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Waist</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.size?.Waist ? viewData?.size?.Waist : "-"}
              </div>
            </div> */}
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button className="btn btn-color" onClick={() => setViewModal(false)}>
            Cancel
          </button>
        </Modal.Footer>
      </Modal>
      
      {/* Sorting Tasks Model */}
      <Modal
        show={shortingModal}
        onHide={() => setShortingModal(false)}
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title>Sorting Tasks</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <DragDropContext onDragEnd={onDragEnd}>
            <Droppable droppableId="droppable">
              {(provided) => (
                <div
                  className="row"
                  {...provided.droppableProps}
                  ref={provided.innerRef}
                >
                  {shortingData?.length > 0 ? (
                    shortingData?.map((item, index) => {
                      return (
                        <Draggable
                          key={item._id}
                          draggableId={item?._id?.toString()}
                          index={index}
                        >
                          {(provided) => (
                            <div
                              className="col-12 d-flex flex-column"
                              // key={index}
                              ref={provided.innerRef}
                              {...provided.draggableProps}
                              {...provided.dragHandleProps}
                            >
                              <div className="shorting d-flex align-items-center ">
                                <img
                                  src={
                                    item?.icon
                                      ? `${Bucket}${item?.icon}`
                                      : "https://static.vecteezy.com/system/resources/thumbnails/014/066/812/small/discount-ribbon-banner-icon-for-graphic-design-logo-website-social-media-mobile-app-ui-illustration-vector.jpg"
                                  }
                                  alt="IMAGE"
                                  className="shorting-icon mr-2"
                                  style={{ width: "60px", height: "60px" }}
                                  // className="rounded-3"
                                />
                                <div className="shorting-details">
                                  <h5>{item?.name}</h5>
                                  <p>{item?.descripiton}</p>
                                </div>
                              </div>
                            </div>
                          )}
                        </Draggable>
                      );
                    })
                  ) : (
                    <div>No Tasks Found</div>
                  )}
                  {provided.placeholder}
                </div>
              )}
            </Droppable>
          </DragDropContext>
        </Modal.Body>
        <Modal.Footer>
          <button
            className="btn btn1 btn-color"
            onClick={() => saveOnShorting()}
          >
            Save
          </button>
        </Modal.Footer>
      </Modal>

      {/* categories sorting model    */}
      <Modal
        show={categoriesShortingModal}
        onHide={() => setCategoriesShortingModal(false)}
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title>Sorting categories</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <DragDropContext onDragEnd={onDragEndCategorie}>
            <Droppable droppableId="droppable">
              {(provided) => (
                <div
                  className="row"
                  {...provided.droppableProps}
                  ref={provided.innerRef}
                >
                  {categoriesShortingData?.length > 0 ? (
                    categoriesShortingData?.map((item, index) => {
                      return (
                        <Draggable
                          key={item._id}
                          draggableId={item?._id?.toString()}
                          index={index}
                        >
                          {(provided) => (
                            <div
                              className="col-12 d-flex flex-column"
                              // key={index}
                              ref={provided.innerRef}
                              {...provided.draggableProps}
                              {...provided.dragHandleProps}
                            >
                              <div className="shorting d-flex align-items-center ">
                                <img
                                  src={
                                    item?.image
                                      ? `${Bucket}${item?.image}`
                                      : "https://static.vecteezy.com/system/resources/thumbnails/014/066/812/small/discount-ribbon-banner-icon-for-graphic-design-logo-website-social-media-mobile-app-ui-illustration-vector.jpg"
                                  }
                                  alt="IMAGE"
                                  className="shorting-icon mr-2"
                                  style={{ width: "60px", height: "60px" }}
                                  // className="rounded-3"
                                />
                                <div className="shorting-details">
                                  <h5>{item?.name}</h5>
                                  <p>{item?.descripiton}</p>
                                </div>
                              </div>
                            </div>
                          )}
                        </Draggable>
                      );
                    })
                  ) : (
                    <div>No Tasks Found</div>
                  )}
                  {provided.placeholder}
                </div>
              )}
            </Droppable>
          </DragDropContext>
        </Modal.Body>
        <Modal.Footer>
          <button
            className="btn btn1 btn-color"
            onClick={() => saveCategoriesShorting()}
          >
            Save
          </button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
