import React, { useState } from "react";
import { Form, FormControl, Button } from "react-bootstrap";
import crossicon from "../../../../../src/media/delete_option.svg";
import { ApiGet } from "../../../../helpers/API/ApiData";

const SingleInputField = (props) => {
  const [dropdownData, setDropdownData] = useState([]);
  const [showDropdown, setShowDropdown] = useState(false);

  const handleFetchData = async () => {
    try {
      const res = await ApiGet("/questions");
      setDropdownData(res?.data?.data || []);
      setShowDropdown(true);
    } catch (err) {
      console.error("Error fetching data:", err);
    }
  };

  return (
    <div
      className="col-md-12"
      key={props.input.id}
      style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between",
        gap: "3rem",
        position: "relative",
      }}
    >
      {props.showQuestionsField && !props.disabled && (
        <Form.Group controlId="english" style={{ width: "57%" }}>
          <Form.Label>Questions:</Form.Label>
          <FormControl
            type="text"
            placeholder="Enter Your Questions"
            value={props.input.label}
            onChange={(event) =>
              props.handleLabelInputChange(props.input.id, event, "singleInput")
            }
          />
        </Form.Group>
      )}

      <Form.Group controlId="english" style={{ width: "57%" }}>
        {console.log(props, "hhelo pro")}
        {props.sort ? (
          <Form.Label>ID: [{props.sort}] English:</Form.Label>
        ) : (
          <Form.Label>ID: [{props.input.name}] English:</Form.Label>
        )}

        <FormControl
          type="text"
          placeholder="Enter English text"
          value={props.input.placeholder}
          onChange={(event) =>
            props.handleEnglishInputChange(props.input.id, event, "singleInput")
          }
        />
      </Form.Group>

      <Form.Group controlId="arabic" style={{ width: "35%" }}>
        <Form.Label>Arabic:</Form.Label>
        <FormControl
          type="text"
          placeholder="أدخل النص بالعربية"
          value={props.input.placeholder_ar}
          onChange={(event) =>
            props.handleArabicInputChange(props.input.id, event, "singleInput")
          }
        />
      </Form.Group>

      <Form.Group controlId="optional" style={{ width: "8%", margin: 0 }}>
        <Form.Check
          type="checkbox"
          label="Optional"
          checked={props.input.optional}
          onChange={(event) =>
            props.handleOptionalChange(props.input.id, event, "singleInput")
          }
        />
      </Form.Group>

      {!props.showQuestionsField && !props.disabled && (
        <Form.Group controlId="connect" style={{ width: "50%", margin: 0 }}>
          <Button
            variant="btn btn1 btn-color font-weight-bolder btn-sm ms-4"
            onClick={() => {
              handleFetchData();
              if (props.handleConnectChange) {
                props.handleConnectChange(props.input.id, "singleInput");
              } else {
                console.error("handleConnectChange is not a function");
              }
            }}
          >
            {props.input.connect ? "Connected" : "Question Connect"}
          </Button>
          {props.input && showDropdown && (
            <select
              style={{ marginLeft: "1rem", width: "82%" }}
              onChange={(e) =>
                props.handleSelectedID(e.target.value, "singleInput")
              }
            >
              <option value="">Select a question</option>
              {dropdownData.map((item) => (
                <option key={item._id} value={item._id}>
                  {item.placeholder}
                </option>
              ))}
            </select>
          )}
        </Form.Group>
      )}

      <div
        // style={{ position: "absolute", left: "-5px", top: "0" }}
        onClick={() => props.handleDeleteInput(props.input.id, "singleInput")}
      >
        <img src={crossicon} alt="mySvgImage" />
      </div>
      <hr />
    </div>
  );
};

export default SingleInputField;
